import ConsultHttpRequest from '../../utils/ConsultHttpRequest';
import Auth from '../../utils/Auth';

import {SNACKBAR_VARIAINT} from '../../utils/Constants';
import {hideLoader, showLoader, showSnackbar} from '../../utils/components/AppLoader/AppLoaderActions'

import {
  // createConsult,
  getAllConsults,
  getAllUpcomingConsults,
  getAllHistoricalConsults
} from "./ConsultActions";

import ConstantMessages from '../../utils/Strings';

let paginationParams = {
  requestConsult:"",
  upcomingConsult:"",
  historicalConsult:""
}

/**
 * Server API call to get all request queue Consults
 * - Dispatch getAllConsults action after successful response from server
 * @returns {function(*)}
 */
export function getAllConsultsRequest(data) {
  const practiceDetails = Auth.getPracticeDetails();
  paginationParams.requestConsult = data;
  let offset = data.page * data.rowsPerPage;
  let limit = offset + data.rowsPerPage;
  const practiceId = practiceDetails.PracticeId;

  return (dispatch) => {
    dispatch(showLoader());
    const url = `/consult/request?practiceId=${practiceId}&offset=${offset}&limit=${limit}`;

    return new ConsultHttpRequest(dispatch, url, 'get')
      .send()
      .then(res => {
        dispatch(getAllConsults(res.data.data));
        dispatch(hideLoader());
        return Promise.resolve(res.data.data);
      }, error => {
        dispatch(hideLoader());
        return Promise.reject(error);
      });
  };
}

