/**
 * Server API call to invite patient
 * - Dispatch addConsult action after successful response from server
 * @returns {function(*)}
 */
export function addConsultRequest(data) {
  return (dispatch) => {
    dispatch(showLoader());
    return new ConsultHttpRequest(dispatch, '/consult/quick', 'post', data)
      .send()
      .then(res => {
        // let consult = res.data.data;
        dispatch(showSnackbar(ConstantMessages.invitePatientForConsultSuccess, SNACKBAR_VARIAINT.SUCCESS));
        // dispatch(createConsult(consult));
        dispatch(getAllConsultsRequest(paginationParams.requestConsult));
        dispatch(getAllUpcomingConsultsRequest(paginationParams.upcomingConsult));
        dispatch(hideLoader());
        return Promise.resolve(res);
      }, error => {
        dispatch(hideLoader());
        return Promise.reject(error);
      });
  };
}

