import {combineReducers} from 'redux';
import auth from './modules/Authentication/AuthenticationReducer';
import dashboard from './modules/Dashboard/DashboardReducer';
import user from './modules/User/UserReducer';
import consults from './modules/Consult/ConsultReducer';
import subscriptions from './modules/SubscriptionPlans/SubscriptionPlansReducer';

import showLoader from './utils/components/AppLoader/AppLoaderReducer';

// Combine all reducers into one root reducer
export default combineReducers({
  auth,
  dashboard,
  user,
  consults,
  subscriptions,
  showLoader
});
