import { SUBSCRIPTION_PLANS } from './SubscriptionPlansActions';

const initialState = {
  subscriptions: []
};

/**
 * User reducer to update the part of state of the application
 * @param state: Current state value
 * @param action: action received
 * @returns {*} current updated state value
 * @constructor
 */
const UserReducer = (state = initialState, action) => {

  switch (action.type) {

    //Store subscription plans
    case SUBSCRIPTION_PLANS:
      return Object.assign({}, state, {
        subscriptions: action.subscriptionPlans
      });

    default:
      return state;
  }
};

export default UserReducer;