import React, {Component} from 'react';
import {OTPublisher} from 'opentok-react'
import './Video.css';
import {Button, Icon} from "antd";

export default class Publisher extends Component {
  constructor(props) {
    super(props);

    this.state = {
      audio: true,
      video: true
    };
  }

  toggleAudio = () => {
    const audio = !this.state.audio;
    this.setState({audio});
  };

  toggleVideo = () => {
    const video = !this.state.video;
    this.setState({video});
  };

  onError = (err) => {
  };

  render() {
    const {audio, video} = this.state;

    return (
      <div>
        <OTPublisher
          className={'video-container'}
          properties={{
            publishAudio: audio,
            publishVideo: video,
            videoSource: undefined
          }}
          onError={this.onError}
        />
        <div className={'video-button-container'}>
          <Button className={`${audio ? 'transparent-background' : 'red-background'} margin-horizontal-10`}
                  onClick={this.toggleAudio}>
            <Icon type={'audio'} className={"color-white"}/>
          </Button>
          <Button className={`${video ? 'transparent-background' : 'red-background'} margin-horizontal-10`}
                  onClick={this.toggleVideo}>
            <Icon type={'video-camera'} className={"color-white"}/>
          </Button>
        </div>
      </div>
    );
  }
}
