import React from 'react';
import './UserProfile.css';
import {connect} from 'react-redux';
import clsx from 'clsx';

import {withStyles} from '@material-ui/core/styles';
import {Button, Grid, Card,CardActions,CardHeader,
  CardContent,Avatar,Divider} from '@material-ui/core';
import Autocomplete from '@material-ui/lab/Autocomplete';

import { ValidatorForm, TextValidator} from 'react-material-ui-form-validator';

import ConstantMessages from '../../../../utils/Strings';

import {
  updateUserRequest,
  getUserPracticeRequest
} from "../../UserApiActions";
import { getAllSpecalitiesRequest } from '../../../Authentication/AuthenticationApiActions';
import {setPageTitle} from "../../../Dashboard/DashboardActions";
import MobileNumber from "../../../../utils/components/MobileNumber/MobileNumber";
import MessageModal from "../../../../utils/components/MessageModal/MessageModal";

import Constants, {ONLY_BLANK_SPACES, ONLY_CHARACTER_ACCEPT} from '../../../../utils/Constants';
import UtilHelper from '../../../../utils/UtilHelper';

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

const styles = theme => ({
  root: {},
  details: {
    display: 'flex'
  },
  avatar: {
    fontSize: '50px',
    height: 200,
    width: 200,
    flexShrink: 0,
    flexGrow: 0,
  },
  progress: {
    marginTop: theme.spacing(2)
  },
  uploadButton: {
    marginRight: theme.spacing(2)
  }
});

class UserProfile extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      user:{
        displayName: '',
        email: '',
        phone: '',
        specialization: [],
        consultationFee: ''
      },
      formSubmit: false,
      validPhoneNumber: true,
      validSpecialization: false
    }
  }

  /**
   * Method executes on onChange action of form element
   */
  handleChange = (event, value) => {
    let validSpecialization= false;
    if(event === "specialization"){
      if(value.length > 0){
        validSpecialization=true;
      }else{
        validSpecialization=false
      }

      const user = Object.assign({}, this.state.user);
      user[event] = value;
      this.setState({ user, validSpecialization });
    }else{
      const user = Object.assign({}, this.state.user);
      user[event.target.name] = event.target.value;
      this.setState({ user, validSpecialization });
    }
  }

  /**
   * Method executes on update logged in user mobile number
   */
  updateMobileNumber = (phone, validPhoneNumber) => {
    const user = Object.assign({}, this.state.user);
    user['phone'] = phone;
    this.setState({user, validPhoneNumber});
  };

  /**
   * Method executes on submit form action
   */
  handleSubmit = () => {
    this.setState({formSubmit: true});
      if (this.state.validPhoneNumber) {
        let formData = UtilHelper.trimObject(this.state.user);
        this.props.updateUserProfile(this.props.loggedInUser.id, formData).then((res) => {
        
        //TODO: Show modal instead of sweet notification
        this.refs.messageModal.setState({
          showModal: true,
          title: ConstantMessages.updateUserNotificationTitle,
          message: ConstantMessages.updateUserSuccessMessage,
          showOkButton: true,
          handleOk: () => {
          },
          okText: 'Ok',
          cancelText: 'Cancel'
        });
      }, (error) => {
      });
    }
  };

  /**
   * Method executes when componnt receive new props
   */
  componentWillReceiveProps = (nextProps) => {

    if (this.props.loggedInUser) {
      if (this.props.loggedInUser.phone !== nextProps.loggedInUser.phone) {
        if (nextProps.loggedInUser.phone !== null)
          this.setState({validPhoneNumber: true});
        else
          this.setState({validPhoneNumber: true});
      } else if (this.state.user.phone === '' && this.props.loggedInUser.phone) {
        if (this.props.loggedInUser.phone !== null)
          this.setState({validPhoneNumber: true});
        else
          this.setState({validPhoneNumber: true});
      }
    }
  }

  /**
   * Remove custom validation rule in ValidatorForm when component is unmount
   * AttachComponent method call when component is unmount
   */
  componentWillUnmount(){
    ValidatorForm.removeValidationRule(ONLY_CHARACTER_ACCEPT);
    ValidatorForm.removeValidationRule(ONLY_BLANK_SPACES);
  }

  /**
   * Change page title when component is render
   * Add custom validation rule in ValidatorForm when component is render
   * AttachComponent method call when component is render
   */
  componentDidMount() {
    ValidatorForm.addValidationRule(ONLY_CHARACTER_ACCEPT, UtilHelper.validateString);
    ValidatorForm.addValidationRule(ONLY_BLANK_SPACES, UtilHelper.validateBlankSpaces);
    this.props.getAllSpecalities().then((res)=>{
      if(res){
        this.setState({
          specialities: res
        })
      }
    });
    this.props.getUserDetailsRequest(this.props.loggedInUser.id).then((res) => {
      res.specialization=[];
      if(res.DoctorSpecialities)
      {
        res.DoctorSpecialities.length > 0 && res.DoctorSpecialities.map((specialization)=>{
          return res.specialization.push(specialization.speciality);
        });
      }

      if(res.specialization.length > 0){
        this.setState({ validSpecialization: true })
      }else{}
      res.roles = [];
      res.Roles.map((role)=>{
        return res.roles.push(role.name);
      })
      
      let user = {
        displayName: res.displayName,
        email: res.email,
        phone: res.phone,
        specialization: res.specialization,
        consultationFee: res.consultationFee
      }

      if (res.hasOwnProperty('phone') && res.phone) {
        this.setState({user, validPhoneNumber: true})
      } else {
        this.setState({user, validPhoneNumber: false})
      }

    }, (error) => {
    });

    this.props.updatePageTitle(this.props.pageTitle, false);
  }

  render() {
    const { specialities, user } = this.state;
    const { loggedInUser, currentRole, classes, className, loggedInUserPractice} = this.props;
    
    return (
      <div className={classes.root}>
        <Grid
          container
          spacing={4}
        >
          <Grid
            item
            lg={4}
            md={6}
            xl={4}
            xs={12}
          >
            <Card
              className={clsx(classes.root, className)}
            >
              <CardContent className="profileImageCenter">
                <Avatar
                  alt={loggedInUser.displayName}
                  className={`${classes.avatar} width-200 height-200 userProfile`}
                  variant="rounded"
                  src={loggedInUser.avatarUrl}
                />
              </CardContent>
            </Card>
          </Grid>
          <Grid
            item
            lg={8}
            md={6}
            xl={8}
            xs={12}
          >
            <Card
              className={`${clsx(classes.root, className)} profileInfoCard`}
            >
              <ValidatorForm
                ref="form"
                onSubmit={this.handleSubmit}
                noValidate
                onError={errors => console.log(errors)}
              >
              <CardHeader
                subheader="The information can be edited"
                title="Profile"
              />
              <Divider />
              <CardContent>
                  <Grid item xs={12}>
                    <TextValidator
                      fullWidth
                      variant="outlined"
                      className="paddingTopProfileElement"
                      label="Name" onChange={this.handleChange}
                      name="displayName" value={user.displayName} 
                      validators={['required', ONLY_BLANK_SPACES, ONLY_CHARACTER_ACCEPT, 'maxStringLength:' + Constants.VALIDATIONS.MAXIMUM_CHARACTERS_TEXT_FIELD]} 
                      errorMessages={['this field is required', Constants.ERROR_MESSAGES.blank_spaces_not_allowed, ConstantMessages.digit_not_allowed, ConstantMessages.maximum_allowed_characters_for_text_field]}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <TextValidator
                      fullWidth
                      variant="outlined"
                      className="paddingTopProfileElement"
                      disabled
                      label="Email" onChange={this.handleChange}
                      name="email" value={user.email} validators={['required', 'isEmail']}
                      errorMessages={['this field is required', 'email is not valid']}
                    />
                  </Grid>
                  <Grid item xs={12} className="paddingTopProfileElement">
                    <MobileNumber phone={user.phone?user.phone:""}
                                  updateMobileNumber={this.updateMobileNumber}/>
                    {
                      this.state.formSubmit && !this.state.validPhoneNumber ?
                        <div className="phone-error">Please enter valid mobile number</div> : ''
                    }
                  </Grid>
                  
                  {
                    currentRole.isDoctor
                    ?(
                      <>
                        <Grid item xs={12} className="paddingTopProfileElement">
                            <Autocomplete
                              multiple
                              id="size-small-outlined-multi"
                              size="small"
                              name="specialization"
                              options={specialities?specialities.map((specialization) => specialization.speciality):[]}
                              onChange={(e, value)=>this.handleChange("specialization", value)}
                              disableClearable
                              defaultValue={user.specialization || null}
                              value={user.specialization || null}
                              renderInput={(params) => {
                                return (
                                  <TextValidator name="specialization" {...params} fullWidth required variant="outlined" 
                                    label="Specialization" placeholder="Specialization" 
                                    validators={['required']}
                                    errorMessages={['this field is required']}
                                    value={user.specialization || null}
                                  />
                                )
                              }}
                            />
                        </Grid>
                        {
                          loggedInUserPractice.Practice.allowPayment
                          ?(
                            <Grid item xs={12}>
                              <TextValidator
                                fullWidth
                                variant="outlined"
                                className="paddingTopProfileElement"
                                type="number"
                                label={`Consultation Fee (${this.props.loggedInUserPractice.Practice.currency})`} onChange={this.handleChange}
                                name="consultationFee" value={user.consultationFee} 
                                validators={['required',ONLY_BLANK_SPACES, 'isFloat', 'maxStringLength:' + Constants.VALIDATIONS.MAXIMUM_CHARACTERS_CONSULT_FEE, 'minNumber:' + Constants.VALIDATIONS.MINIMUM_CONSULT_FEE]}
                                errorMessages={['this field is required', Constants.ERROR_MESSAGES.blank_spaces_not_allowed, Constants.ERROR_MESSAGES.only_number_accept, ConstantMessages.maximum_allowed_characters_for_consult_fee, ConstantMessages.minimum_amount_greter_than_zero]}
                              />
                            </Grid>
                          )
                          :<></>
                        }
                      </>
                    )
                    :<></>
                  }

              </CardContent>
              
              <Divider />
              <CardActions>
                <Button
                  type="submit"
                  color="primary"
                  variant="contained"
                  className="form-button"
                >
                  Save details
                </Button>
              </CardActions>
              </ValidatorForm>
            </Card>
          </Grid>
        </Grid>

        <MessageModal
            ref="messageModal" />
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    loggedInUser: state.auth.loggedInUser,
    profileImg: state.auth.profileImg,
    currentRole: state.auth.currentRole,
    specialities: state.auth.specialities,
    loggedInUserPractice: state.auth.loggedInUserPractice
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    getUserDetailsRequest: (id) => {
      return dispatch(getUserPracticeRequest(id)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },

    updateUserProfile: (id, data) => {
      return dispatch(updateUserRequest(id, data)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },

    updatePageTitle: (pageTitle, enablePageTitle) => {
      dispatch(setPageTitle(pageTitle, enablePageTitle));
    },

    getAllSpecalities: () => {
      return dispatch(getAllSpecalitiesRequest()).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },
  }
};

UserProfile = connect(
  mapStateToProps,
  mapDispatchToProps
)(UserProfile);

export default withStyles(styles)(UserProfile);
