/**
 * Server API call to get all specialities
 * - Dispatch getAllSpecialities action after successful response from server
 * @returns {function(*)}
 */
export function getAllSpecalitiesRequest(data) {
  return (dispatch) => {
    dispatch(showLoader());
    const url = '/practice/specialities';

    return new PracticeHttpRequest(dispatch, url, 'get')
      .send()
      .then(res => {
        dispatch(getAllSpecalities(res.data.data));
        dispatch(hideLoader());
        return Promise.resolve(res.data.data);
      }, error => {
        dispatch(hideLoader());
        return Promise.reject(error);
      });
  };
}