import React from 'react';
import {connect} from 'react-redux';
import {Redirect} from "react-router-dom";
import queryString from 'querystring';

import Topbar from '../../../../utils/components/Topbar/Topbar';

import AppLoader from "../../../../utils/components/AppLoader/AppLoader";

import {getUserPracticeRequest} from '../../../User/UserApiActions';
import { saveConnectAccount } from '../../../SubscriptionPlans/SubscriptionPlansApiActions';

class SetupStripeXonnectaccount extends React.Component{

    constructor(props) {
        super(props);
        this.state = {
          isStripeConnect: false,
          showLoader: false,
          urlNotFound: false
        };
    }

    /**
     * Save stripe connect account details of the logged in user when component is render
     * AttachComponent method call when component is render
     */
    componentDidMount = () => {
        this.props.getUserRequest(this.props.loggedInUser.id).then((response)=>{
            const { isStripeConnect } = this.state;
            let params = queryString.parse(this.props.location.search)
            if(!isStripeConnect && params['?code']){
                this.setState({showLoader: true}, () => {
                    if(this.state.showLoader){
                        let practiceId = response.PracticeId;
                        let code = params['?code'];
                        let state = params['state'];
                        this.props.saveConnectAccount(practiceId, code, state).then((res)=>{
                            if(res){
                                this.setState({
                                    isStripeConnect: true,
                                    showLoader: false
                                })
                            }
                        }, (error) => {
                        });
                    }
                })  
            }else{
                if(!params['?code']){
                    this.setState({
                        urlNotFound: true
                    })
                }
            }
        });
    }

    render(){
        const { isStripeConnect, urlNotFound } = this.state;
        const redirectUrl = sessionStorage.getItem('redirectUrl');
            
        if(!isStripeConnect){
            if(urlNotFound){
                return (<Redirect to="/"/>);
            }else{
                return(
                    <>
                        <Topbar pageTitle="Setup Stripe Connect Account" />
                        <AppLoader showLoader={this.state.showLoader}/>
                    </>
                )
            }
            
        }else{

            return (<Redirect to={ redirectUrl }/>);
        }
        
    }

}

const mapStateToProps = state => {
    return {
      loggedInUser: state.auth.loggedInUser,
      currentRole: state.auth.currentRole,
      loggedInUserPractice: state.auth.loggedInUserPractice
    }
};

const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    getUserRequest: (id) => {
      return dispatch(getUserPracticeRequest(id)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },
    saveConnectAccount: (practiceId, code, state) => {
      return dispatch(saveConnectAccount(practiceId, code, state)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },
  }
};

SetupStripeXonnectaccount = connect(
  mapStateToProps,
  mapDispatchToProps
)(SetupStripeXonnectaccount);

export default SetupStripeXonnectaccount;