import React from 'react';

import {connect} from 'react-redux';
import clsx from 'clsx';
import {withStyles} from '@material-ui/core/styles';
import {
    Button, Grid, Dialog, DialogContent, Typography, IconButton,
    DialogTitle, Card, CardHeader, CardContent, CardActions
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';

import BillingForm from '../BillingForm/BillingForm';
import moment from 'moment';

import {StripeProvider, Elements} from 'react-stripe-elements';
import { getPracticeDetailsRequest } from '../../PracticeApiActions';
// const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));

import {
    getUserPracticeRequest,
} from '../../../User/UserApiActions';
import {
    getAllSubscriptionPlansRequest,
    purchaseSubscriptionPlansRequest
} from '../../../SubscriptionPlans/SubscriptionPlansApiActions';
import { cancelSubscriptionPlansRequest } from '../../../SubscriptionPlans/SubscriptionPlansApiActions';

import AppLoader from "../../../../utils/components/AppLoader/AppLoader";
import MessageModal from "../../../../utils/components/MessageModal/MessageModal";

const dialogTitleStyles = (theme) => ({
    root: {
      margin: 0,
      padding: theme.spacing(2),
    },
    closeButton: {
      position: 'absolute',
      right: theme.spacing(1),
      top: theme.spacing(1),
      color: theme.palette.grey[500],
    },
});

const CustomeDialogTitle = withStyles(dialogTitleStyles)((props) => {
    const { children, classes, onClose, ...other } = props;
    return (
      <DialogTitle disableTypography className={classes.root} {...other}>
        <Typography variant="h6">{children}</Typography>
        {onClose ? (
          <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
            <CloseIcon />
          </IconButton>
        ) : null}
      </DialogTitle>
    );
});

const styles = theme => ({
    root: {
      flexGrow: 1,
    },
    paper: {
      padding: theme.spacing(2),
      color: theme.palette.text.secondary,
    }
});

class SetupPracticeStepThree extends React.Component{

    constructor(props) {
        super(props);
        this.state = {
            subscription:'',
            validFields:{
                subscription:false,    
            },
            practiceDetails: undefined
        };
    }

    /**
     * Method is used for hide loader
     */
    hideLoader = () => {
        this.setState({ showLoader: false });
    };

    /**
     * Method is used for show loader
     */
    showLoader = () => {
        this.setState({ showLoader: true });
    };

    /**
     * Get user practice details of logged in user when component is render
     * Get all subscription plans when component is render 
     * AttachComponent method call when component is render
     */
    componentDidMount() {
        if(this.props.loggedInUser){
            const {id: userId} = this.props.loggedInUser;
            this.props.getUserRequest(userId).then((res)=>{
                if(res){
                    if(this.props.loggedInUserPractice.Practice.stripeSubscriptionId){
                        this.props.getPracticeDetails(this.props.loggedInUserPractice.PracticeId).then((res)=>{
                            this.setState({
                                practiceDetails: res
                            })
                        })
                    }else{}
                    
                    if(this.props.loggedInUser.isSubscription){
                        this.props.getAllSubscriptionPlans();
                    }
                }
            });
        }
    }

    /**
     * Method executes when select a subscription
     */
    onSubscriptionSelect = (subscription) => {
        let validFields = Object.assign({}, this.state.validFields);
        validFields.subscription=true;
        this.setState({
            subscription: subscription,
            validFields
        })
    }
    
    /**
     * Method is used for cancel to purchase subasription
     */
    handleClose = () => {
        let validFields = Object.assign({}, this.state.validFields);
        validFields.subscription=false;
        this.setState({
            subscription: '',
            validFields
        })
    }

    /**
     * Method is used for process the payment request on stripe
     */
    processPaymentRequest = (token) => {
        if(this.props.loggedInUserPractice && this.props.loggedInUserPractice.PracticeId){
            if(this.state.validFields.subscription) {
                let data ={
                    practiceId: this.props.loggedInUserPractice.PracticeId,
                    subscriptionPlanId:this.state.subscription.id,
                    token:token
                }
              this.props.purchaseSubscriptionPlans(data).then(res => {
        
                this.hideLoader();
                this.handleClose();
        
                this.refs.messageModal.setState({
                  showModal: true, message: 'Plan subscribed successfully.', title: 'Subscription',
                  showOkButton: true,
                  showCancelButton: false,
                  handleOk: () => {
                    if(this.props.nextButtonClick){
                        this.props.nextButtonClick()
                    }else{
                        if(this.props.loggedInUserPractice.Practice.stripeSubscriptionId){
                            this.props.getPracticeDetails(this.props.loggedInUserPractice.PracticeId).then((res)=>{
                                this.setState({
                                    practiceDetails: res
                                })
                            })
                        }else{}
                    }
                  },
                  okText: 'Ok'
                });
        
              }, err => {
                this.hideLoader();
              })
            }
        }
    }

    /**
     * Method is used to get the token from the stripe for the transaction
     */
    handleTokenCreation = (token) => {
        if(token.hasOwnProperty('token') && token.token.hasOwnProperty('id')) {
          this.setState({ showLoader: true }, () => {
            this.processPaymentRequest(token.token.id);
          });
        } else {
          this.hideLoader();
        }
    };

    /**
     * Method is used for cancel purchase subscription
     */
    onCancelSubscription = (subscription) => {
        this.refs.messageModal.setState({
            showModal: true, message: 'Are you sure, you went to cancel subscription?', title: 'Cancel Subscription',
            showOkButton: true,
            showCancelButton: true,
            handleOk: () => {
                this.props.cancelSubscriptionPlans(this.props.loggedInUserPractice.PracticeId).then((res) => {
                    if(res){
                        this.refs.messageModal.setState({
                            showModal: true, message: 'You have successfully unsubscribed from your current plan: however you will be able to access all available features untile the next billing period.', title: 'Cancel Subscription',
                            showOkButton: true,
                            showCancelButton: false,
                            handleOk: () => {
                                if(this.props.loggedInUserPractice.Practice.stripeSubscriptionId){
                                    this.props.getPracticeDetails(this.props.loggedInUserPractice.PracticeId).then((res)=>{
                                        this.setState({
                                            practiceDetails: res
                                        })
                                    })
                                }else{}
                            },
                            okText: 'Ok'
                        });
                    }
                });
            },
            handleCancel: () => {

            },
            okText: 'Yes',
            cancelText: 'No'
        });
    }

    render(){
        const { validFields, practiceDetails } = this.state;
        const { classes, subscriptions, className } = this.props;
        return(
            <div className="subscriptionsContainer">
                <Grid container spacing={3}>
                    <Grid item xs={12} className="informationHeader subscriptionTitle">
                        Subscriptions
                    </Grid>
                    {
                        subscriptions && subscriptions.length > 0 && subscriptions.map((subscription, index) => {
                            return (
                                <Grid key={index} xs={12} sm={6} md={4} lg={4} xl={3} item className="">
                                    <Card
                                    className={`${clsx(classes.root, className)} singleSubscriptionCard ${practiceDetails?practiceDetails.subscriptions.id === subscription.id?"activeSubscription":"":""}`}
                                    >
                                        <CardHeader
                                            className="subscriptionHeader"
                                            title={ subscription.name }
                                        />
                                        <CardContent className="subscriptionContent">
                                            <div>
                                                <span className="subscriptionPrice">
                                                    ${ subscription.amount/100 }
                                                </span>
                                                <span>/{subscription.interval}</span>
                                            </div>
                                            {
                                                practiceDetails && practiceDetails.subscriptions.id === subscription.id
                                                ?(  <div className="ActiveSubscriptionDetails">
                                                        <div className="subscriptionStatus">
                                                            <span>
                                                                Status :
                                                            </span>
                                                            <span>
                                                                { practiceDetails.subscriptions.status }
                                                            </span>
                                                        </div>
                                                        <div className="subscriptionStartDate">
                                                            <span>
                                                                Start Date :
                                                            </span>
                                                            <span>
                                                                {
                                                                    moment.unix(practiceDetails.subscriptions.startTimestamp).format("Do MMMM YYYY")
                                                                }
                                                            </span>
                                                        </div>
                                                        <div className="subscriptionEndDate">
                                                            <span>
                                                                End Date :
                                                            </span>
                                                            <span>
                                                                {
                                                                    moment.unix(practiceDetails.subscriptions.endTimestamp).format("Do MMMM YYYY")
                                                                }
                                                            </span>
                                                        </div>
                                                        {
                                                            practiceDetails && practiceDetails.subscriptions.cancelAtPeriodEnd
                                                            ?(
                                                                <div className="subscriptionExpiresOn">
                                                                    <span>
                                                                        Expires On :
                                                                    </span>
                                                                    <span>
                                                                        {
                                                                            moment.unix(practiceDetails.subscriptions.endTimestamp).format("Do MMMM YYYY")
                                                                        }
                                                                    </span>
                                                                </div>
                                                            )
                                                            :<></>
                                                        }

                                                    </div>
                                                )
                                                :""
                                            }
                                            
                                        </CardContent>
                                        <CardActions className="subscriptionActions">
                                            {
                                                practiceDetails && practiceDetails.subscriptions.id === subscription.id
                                                ?(
                                                    practiceDetails && !practiceDetails.subscriptions.cancelAtPeriodEnd
                                                    ?(
                                                        <Button disabled={ !subscription.isActive } variant="contained" color="inherit" onClick={ () => this.onCancelSubscription(subscription) }>
                                                            Cancel
                                                        </Button>
                                                    )
                                                    :<></>
                                                )
                                                :(
                                                    <Button disabled={ !subscription.isActive } variant="contained" color="primary" onClick={ () => this.onSubscriptionSelect(subscription) }>
                                                        Buy
                                                    </Button>
                                                )
                                            }
                                        </CardActions>
                                    </Card>
                                </Grid>
                            )        
                        })
                    }
                </Grid>

                <Dialog
                    // fullScreen={fullScreen}
                    maxWidth="sm"
                    fullWidth={true}
                    open={validFields.subscription}
                    // onClose={this.handleClose}
                    aria-labelledby="responsive-dialog-title"
                >
                    <CustomeDialogTitle id="responsive-dialog-title" onClose={this.handleClose}>{"Billing Information"}</CustomeDialogTitle>
                    <DialogContent>
                        <StripeProvider apiKey={process.env.REACT_APP_STRIPE_API_KEY}>
                            <Elements>
                                <BillingForm handleTokenCreation={(data)=>this.handleTokenCreation(data)} showLoader = {this.showLoader} hideLoader = {this.hideLoader}/>
                            </Elements>
                        </StripeProvider>
                    </DialogContent>
                </Dialog>

                <MessageModal
                    ref="messageModal" />

                <AppLoader showLoader={this.state.showLoader}/>
            </div>
        )
    }

}

const mapStateToProps = (state) => {
    return {
        loggedInUser: state.auth.loggedInUser,
        currentRole: state.auth.currentRole,
        loggedInUserPractice: state.auth.loggedInUserPractice,
        subscriptions: state.subscriptions.subscriptions
    }
};
  
const mapDispatchToProps = (dispatch) => {
    return {
        getUserRequest: (id) => {
          return dispatch(getUserPracticeRequest(id)).then((res) => {
            return Promise.resolve(res);
          }, (error) => {
            return Promise.reject(error);
          });
        },
        getAllSubscriptionPlans: () => {
            return dispatch(getAllSubscriptionPlansRequest()).then((res) => {
              return Promise.resolve(res);
            }, (error) => {
              return Promise.reject(error);
            });
        },
        purchaseSubscriptionPlans: (data) => {
            return dispatch(purchaseSubscriptionPlansRequest(data)).then((res) => {
              return Promise.resolve(res);
            }, (error) => {
              return Promise.reject(error);
            });
        },
        getPracticeDetails: (practiceId) => {
            return dispatch(getPracticeDetailsRequest(practiceId)).then((res) => {
              return Promise.resolve(res);
            }, (error) => {
              return Promise.reject(error);
            });
        },
        cancelSubscriptionPlans: (practiceId) => {
            return dispatch(cancelSubscriptionPlansRequest(practiceId)).then((res) => {
              return Promise.resolve(res);
            }, (error) => {
              return Promise.reject(error);
            });
        }
    }
}
SetupPracticeStepThree = connect(
    mapStateToProps,
    mapDispatchToProps
)(SetupPracticeStepThree);

export default withStyles(styles)(SetupPracticeStepThree);