import React from 'react';
import './SocialSignUp.css';

import {withStyles} from '@material-ui/core/styles';
import { Grid, Button } from '@material-ui/core';

import FacebookLogin from 'react-facebook-login/dist/facebook-login-render-props';
import { GoogleLogin } from 'react-google-login';

import { Facebook as FacebookIcon, Google as GoogleIcon } from '../../../../utils/components/icons';


const styles = theme => ({
    grid: {
      height: '100%'
    },
    quoteContainer: {
      [theme.breakpoints.down('md')]: {
        textAlign: 'center'
      }
    },
    socialButtons: {
        [theme.breakpoints.down('md')]: {
            display: 'block',
            paddingRight: "5%",
            paddingLeft: "5%"
        }
    },
    socialIcon: {
      marginRight: theme.spacing(1),
    }
});
class SocialSignUp extends React.Component {

  constructor() {
      super();
      this.state = { isAuthenticated: false, user: null, token: ''};
  }

  /**
   * Method executes when recive success/failure call back from the facebook sign in
   */
  facebookResponse = (res) => {
    if(res && res.accessToken){
        this.props.signInWithFacebook(res);
    }
  };

  /**
   * Method executes when recive success call back from the google sign in
   */
  googleResponse = (res) => {
    if(res){
        this.props.signInWithGoogle(res);
    }
  };

  /**
   * Method executes when recive failure call back from the google sign in
   */
  onFailure = (error) => {
    console.log(error);
  }

  render() {
    const { classes } = this.props;
      return (
            <Grid
            className={classes.grid}
            container
            >
                <Grid
                  container
                  className={classes.socialButtons}
                  spacing={2}
                >
                    <Grid item lg={3} ></Grid>
                    <Grid
                        className={classes.quoteContainer}
                        item
                        lg={6}
                    >
                        <FacebookLogin
                            appId={process.env.REACT_APP_FACEBOOK_APP_ID}
                            autoLoad={false}
                            fields="name,email,picture"
                            callback={this.facebookResponse}
                            render={ renderProps => (
                                <Button
                                    color="primary"
                                    onClick={renderProps.onClick}
                                    size="large"
                                    className="googleBtn"
                                    fullWidth
                                    variant="contained"
                                >
                                    <FacebookIcon className={classes.socialIcon} />
                                    Continue with Facebook
                                </Button>
                            )}
                        />
                    </Grid>
                    <Grid item lg={3} ></Grid>
                    <Grid item lg={3} ></Grid>
                    <Grid
                        className={classes.quoteContainer}
                        item
                        lg={6}
                    >
                        <GoogleLogin
                            clientId={process.env.REACT_APP_GOOGLE_CLIENT_ID}
                            buttonText="GOOGLE"
                            onSuccess={this.googleResponse}
                            onFailure={this.onFailure}
                            render={ renderProps => (
                                <Button
                                    onClick={renderProps.onClick}
                                    size="large"
                                    className="googleBtn"
                                    fullWidth
                                    variant="contained"
                                >
                                    <GoogleIcon className={classes.socialIcon} />
                                    Continue with Google
                                </Button>
                            )}
                        />
                    </Grid>
                    <Grid item lg={3} ></Grid>
                </Grid>
            </Grid>
        );
  }
}

export default withStyles(styles)(SocialSignUp);
