import React, {Component} from 'react';

import {OTSubscriber} from 'opentok-react';

export default class Subscriber extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <OTSubscriber
        retry={true}
        maxRetryAttempts={3}
        retryAttemptTimeout={2000}
      />
    );
  }
}
