import Constants from '../../utils/Constants';

// Export Constants (Action names)
export const GET_ALL_USERS = 'GET_ALL_USERS';
export const GET_ALL_DOCTOR_USERS = 'GET_ALL_DOCTOR_USERS';
export const CREATE_USER = 'CREATE_USER';
export const UPDATE_USER = 'UPDATE_USER';
export const UPDATE_CURRENT_USER = 'UPDATE_CURRENT_USER';

/**
 * Action called after get all users
 * @param users
 */
export function getAllUsers(users) {
  const data = users;
  const usersToDisplay = users.rows.map((user) => {

    let humanReadableRoles = []
    user.Roles.map((role) => {
      return humanReadableRoles.push(Constants.HUMAN_READABLE_SYSTEM_ROLES[role.name])  
    })
    
    user['humanReadableRoles'] = humanReadableRoles;

    return user
  });

  data.rows = usersToDisplay
  return {
    type: GET_ALL_USERS,
    users: data
  };
}

/**
 * Action called after get all users doctors
 * @param users
 */
export function getAllDoctorUsers(users) {
  return {
    type: GET_ALL_DOCTOR_USERS,
    users: users
  };
}

/**
 * Action called after create/invite user
 * @param user
 */

export function createUser(user) {

  let humanReadableRoles = []
  user.Roles.map((role) => {
    return humanReadableRoles.push(Constants.HUMAN_READABLE_SYSTEM_ROLES[role.name])  
  })
  
  user['humanReadableRoles'] = humanReadableRoles;

  return {
    type: CREATE_USER,
    user
  };
}

