// Export Constants (Action names)
export const GET_ALL_CONSULTS = 'GET_ALL_CONSULTS';
export const GET_ALL_UPCOMING_CONSULTS = 'GET_ALL_UPCOMING_CONSULTS';
export const GET_ALL_HISTORICAL_CONSULTS = 'GET_ALL_HISTORICAL_CONSULTS';
export const CREATE_CONSULT = 'CREATE_CONSULT';
export const CONFIRM_CONSULTS = 'CONFIRM_CONSULTS';
export const REJECT_CONSULT = 'REJECT_CONSULT';

/**
 * Action called after get all requect queue consult api call
 * @param consults list
 */
export function getAllConsults(consults) {
  return {
    type: GET_ALL_CONSULTS,
    consults
  };
}

/**
 * Action called after get all upcoming consults api call
 * @param upcomingConsults list
 */
export function getAllUpcomingConsults(upcomingConsults) {
  return {
    type: GET_ALL_UPCOMING_CONSULTS,
    upcomingConsults
  };
}

/**
 * Action called after get all historical consults api call
 * @param historicalConsults list
 */
export function getAllHistoricalConsults(historicalConsults) {
  return {
    type: GET_ALL_HISTORICAL_CONSULTS,
    historicalConsults
  };
}

/**
 * Action called after successfully invite patient for consult api call
 * @param consult
 */
export function createConsult(consult) {

  return {
    type: CREATE_CONSULT,
    consult
  };
}

/**
 * Action called after accept appoinment/consult api call
 * @param consult
 */
export function confirmConsult(consult) {

  return {
    type: CONFIRM_CONSULTS,
    consult
  };
}

/**
 * Action called after reject appoinment/consult api call
 * @param specialities list
 */
export function rejectConsult(consult) {

  return {
    type: REJECT_CONSULT,
    consult
  };
}