import PracticeHttpRequest from '../../utils/PracticeHttpRequest';
import {hideLoader, showLoader} from '../../utils/components/AppLoader/AppLoaderActions'
import { setDashboardStats } from './DashboardActions';

/**
 * Server API call to get practice/co-ordinators dashboard summary/stats
 * - Dispatch getPracticeSummary action after successful response from server
 * @returns {function(*)}
 */
export function getPracticeSummary(practiceId) {
  return (dispatch) => {
    dispatch(showLoader());
    return new PracticeHttpRequest(dispatch, `/practice/stats/practice/${practiceId}`, 'get')
      .send()
      .then(res => {
        dispatch(setDashboardStats(res.data.data));
        dispatch(hideLoader());
        return Promise.resolve(res.data.data);
      }, error => {
        dispatch(hideLoader());
        return Promise.reject(error);
      });
  };
}

