import {CREATE_CONSULT, GET_ALL_CONSULTS, GET_ALL_UPCOMING_CONSULTS, GET_ALL_HISTORICAL_CONSULTS, CONFIRM_CONSULTS, REJECT_CONSULT} from './ConsultActions';
import {SIGN_OUT} from "../Authentication/AuthenticationActions";

const initialState = {
  consults: [],
  upcomingConsults: [],
  historicalConsults: []
};

/**
 * User reducer to update the part of state of the application
 * @param state: Current state value
 * @param action: action received
 * @returns {*} current updated state value
 * @constructor
 */
const UserReducer = (state = initialState, action) => {

  switch (action.type) {

    //Get list of request queue consults to be shown
    case GET_ALL_CONSULTS:
      return Object.assign({}, state, {
        consults: action.consults
      });

    //Get list of upcoming consults to be shown
    case GET_ALL_UPCOMING_CONSULTS:
      return Object.assign({}, state, {
        upcomingConsults: action.upcomingConsults
      });
    
    //Get list of historical consults to be shown
    case GET_ALL_HISTORICAL_CONSULTS:
      return Object.assign({}, state, {
        historicalConsults: action.historicalConsults
      });

    //Append newly created consult/invited patient
    case CREATE_CONSULT:
      return Object.assign({}, state, {
        consults: [...state.consults.rows, action.consult]
      });

    //Confirm consult
    case CONFIRM_CONSULTS:
      let consults = state.consults;

      consults.rows = consults.rows.map((consult) => {
        if (consult.id === action.consult.id) {
          consult.status = action.consult.status;
        }
        return consult;
      });

      return Object.assign({}, state, {
        consults: {...state.consults, rows: consults.rows}
      });

    //Reject consult
    case REJECT_CONSULT:
      consults = state.consults;

      consults.rows = consults.rows.map((consult) => {
        if (consult.id === action.consult.id) {
          consult.status = action.consult.status;
        }
        return consult;
      });

      return Object.assign({}, state, {
        consults: {...state.consults, rows: consults.rows}
      });

    case SIGN_OUT:
      return Object.assign({}, initialState);

    default:
      return state;
  }
};

export default UserReducer;