import React, {Component} from 'react';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import SoapForm from "../SoapForm/SoapForm";
import Document from "../Document/Document";
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import './ConsultForm.css';

const TabPanel = (props) => {
  const {children, value, index, ...other} = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`consult-tabpanel-${index}`}
      aria-labelledby={`consult-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  );
};

export default class ConsultForm extends Component {
  constructor(props) {
    super(props);

    this.state = {
      value: 0
    };
  }

  handleChange = (event, newValue) => {
    this.setState({value: newValue});
  };

  render() {
    const {value} = this.state;

    return (
      <>
      <AppBar position="static" className={'consult-tabs'}>
        <Tabs
          value={value}
          onChange={this.handleChange}
          indicatorColor="primary"
          textColor="primary"
          centered>
          <Tab label="Notes"/>
          <Tab label="Documents"/>
        </Tabs>
      </AppBar>

      <TabPanel value={value} index={0}>
          <SoapForm consultId={this.props.consultId} />
        </TabPanel>
        <TabPanel value={value} index={1}>
          <Document consultId={this.props.consultId} />
        </TabPanel>
      </>
    );
  }
}
