import React from "react";
import {BrowserRouter as Router, Route, Switch} from "react-router-dom";

import {ACTIONS, ROUTE_ACTIONS} from './utils/Constants';

import Home from './modules/Dashboard/components/Home/Home';
import Dashboard from './modules/Dashboard/components/Dashboard/Dashboard';
import PageNotFound from './modules/Authentication/components/PageNotFound/PageNotFound';
import SignIn from './modules/Authentication/components/SignIn/SignIn';
import SetupPractice from './modules/Practice/components/SetupPractice/SetupPractice';
import SetupStripeConnectAccount from './modules/Practice/components/SetupPractice/SetupStripeConnectAccount';
import Consult from "./modules/Consult/components/Consult/Consult";
import ConsultHistory from "./modules/Consult/components/ConsultHistory/ConsultHistory";
import TermsOfUsage from "./utils/components/TermsOfUsage/TermsOfUsage";
import PrivacyPolicy from "./utils/components/PrivacyPolicy/PrivacyPolicy";

/**
 * Used decide the component to render depending on the current route of the application
 * @returns {XML}
 * @constructor
 */
function Routes() {
  return (
    <Router>
      <Switch>
        <Route exact path="/" component={Home}/>
        <Route path={ACTIONS.SIGN_IN} component={SignIn}/>
        <Route path={ROUTE_ACTIONS.CONSULT} component={(props) => <Consult {...props} />}/>
        <Route path={ROUTE_ACTIONS.TERMS_OF_USAGE} component={(props) => <TermsOfUsage {...props} />}/>
        <Route path={ROUTE_ACTIONS.PRIVACY_POLICY} component={(props) => <PrivacyPolicy {...props} />}/>
        <Route path={ROUTE_ACTIONS.CONSULT_HISTORY} component={(props) => <ConsultHistory {...props} />}/>
        <Route path={ROUTE_ACTIONS.DASHBOARD} component={(props) => <Dashboard {...props} menu={'dashboard'} pageTitle="Dashboard"/>}/>
        <Route path={ROUTE_ACTIONS.SETUP_PRACTICE} component={(props) => <SetupPractice {...props} />} />
        <Route path={ROUTE_ACTIONS.SETUP_STRIPE_CONNECT_ACCOUNT} component={(props) => <SetupStripeConnectAccount {...props} />} />

        <Route path={ACTIONS.UPCOMING_CONSULTS} component={(props) => <Dashboard {...props} menu={'upcomingConsults'} pageTitle="Upcoming Consults"/>}/>
        <Route path={ACTIONS.HISTORICAL_CONSULTS} component={(props) => <Dashboard {...props} menu={'historicalConsults'} pageTitle="Historical Consults"/>}/>
        <Route path={ACTIONS.ACCOUNT} component={(props) => <Dashboard {...props} menu={'account'} pageTitle="Account"/>}/>

        <Route path={ACTIONS.PROFILE_VIEW} component={(props) => <Dashboard {...props} menu={'profile'} pageTitle="Profile"/>}/>
        <Route path={ACTIONS.PRACTICE_PROFILE_VIEW} component={(props) => <Dashboard {...props} menu={'practiceProfile'} pageTitle="Practice Profile"/>}/>
        <Route exact path={ACTIONS.USER_LIST} component={(props) => <Dashboard {...props} menu={'users'} pageTitle="Users"/>}/>
        <Route component={PageNotFound}/>
      </Switch>
    </Router>
  );
}

export default Routes;
