import PaymentHttpRequest from '../../utils/PaymentHttpRequest';

import {SNACKBAR_VARIAINT} from '../../utils/Constants';
import {hideLoader, showLoader, showSnackbar} from '../../utils/components/AppLoader/AppLoaderActions'

import {
  getAllSubscriptionPlans
} from "./SubscriptionPlansActions";

import { saveStripConnectAccountId } from "../Authentication/AuthenticationActions"
import ConstantMessages from '../../utils/Strings';

/**
 * Server API call to fetch an subscription plans
 * - Dispatch getAllSubscriptionPlans action after successful response from server
 * @returns {function(*)}
 */
export function getAllSubscriptionPlansRequest() {
  return (dispatch) => {
    dispatch(showLoader());
    return new PaymentHttpRequest(dispatch, `/payment/subscriptionPlans`, 'get', {})
      .send()
      .then(res => {
        dispatch(getAllSubscriptionPlans(res.data.data));
        dispatch(hideLoader());
        return Promise.resolve(res.data.data);
      }).catch((error) => {
        dispatch(hideLoader());
        return Promise.reject(error);
      });
  };
}

/**
 * Server API call to purchase an subscription plan
 * @returns {function(*)}
 */
export function purchaseSubscriptionPlansRequest(data) {
  return (dispatch) => {
    dispatch(showLoader());
    return new PaymentHttpRequest(dispatch, `/payment/subscribePlan`, 'post', data)
      .send()
      .then(res => {
        dispatch(showSnackbar(ConstantMessages.suscribePlanSuccessfullyPurchase, SNACKBAR_VARIAINT.SUCCESS));
        // dispatch(saveSubscriptionId(res.data.data.id));
        dispatch(hideLoader());
        return Promise.resolve(res.data.data);
      }).catch((error) => {
        dispatch(hideLoader());
        return Promise.reject(error);
      });
  };
}

/**
 * Server API call to cancel current activate subscription plans
 * @returns {function(*)}
 */
export function cancelSubscriptionPlansRequest(practiceId) {
  let data ={
    practiceId: practiceId
  }
  return (dispatch) => {
    dispatch(showLoader());
    let url = `/payment/cancelCurrentSubscriptionPlan`
    return new PaymentHttpRequest(dispatch, url, 'post', data)
      .send()
      .then(res => {
        dispatch(showSnackbar(ConstantMessages.suscribePlanSuccessfullyCancel, SNACKBAR_VARIAINT.SUCCESS));
        // dispatch(saveSubscriptionId(res.data.data.id));
        dispatch(hideLoader());
        return Promise.resolve(res.data.data);
      }).catch((error) => {
        dispatch(hideLoader());
        return Promise.reject(error);
      });
  };
}

/**
 * Server API call to save stripe connect account
 * - Dispatch saveStripConnectAccountId action after successful response from server
 * @returns {function(*)}
 */
export function saveConnectAccount(practiceId, authorizationCode, state) {
  let url = `/payment/saveConnectAccount?practiceId=${practiceId}&authorizationCode=${authorizationCode}&state=${state}`
  return (dispatch) => {
    dispatch(showLoader());
    return new PaymentHttpRequest(dispatch, url, 'get')
      .send()
      .then(res => {
        dispatch(showSnackbar(ConstantMessages.stripeConnectAccountAccountCreatedSuccessfully, SNACKBAR_VARIAINT.SUCCESS));
        dispatch(saveStripConnectAccountId(res.data.data.id));
        dispatch(hideLoader());
        return Promise.resolve(res.data.data);
      }).catch((error) => {
        dispatch(hideLoader());
        return Promise.reject(error);
      });
  }
}

/**
 * Server API call to get stripe connect account
 * @returns {function(*)}
 */
export function getConnectAccountRequest(practiceId) {
  let url = `/payment/getConnectAccount?practiceId=${practiceId}`
  return (dispatch) => {
    dispatch(showLoader());
    return new PaymentHttpRequest(dispatch, url, 'get')
      .send()
      .then(res => {
        dispatch(hideLoader());
        return Promise.resolve(res.data.data);
      }).catch((error) => {
        dispatch(hideLoader());
        return Promise.reject(error);
      });
  }
}