import React from 'react';

import {connect} from 'react-redux';
import { Grid, Button, Typography } from '@material-ui/core';
import clsx from 'clsx';
import StripeLogo from '../../../../assets/Stripe_Logo.png';
import { getPracticeDetailsRequest } from '../../PracticeApiActions';
import { getConnectAccountRequest } from '../../../SubscriptionPlans/SubscriptionPlansApiActions';
import MessageModal from "../../../../utils/components/MessageModal/MessageModal";
import querystring from 'querystring';

import Stripe_Logo from '../../../../assets/Stripe_Logo.png'
import {withStyles} from '@material-ui/core/styles';

import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';

const styles = theme => ({
    root: {
        backgroundColor: "#fff",
        boxShadow: "0 0 0 1px rgba(63,63,68,0.05), 0 1px 3px 0 rgba(63,63,68,0.15)",
        borderRadius: '4px'
    },
    media: {
      paddingTop: theme.spacing(2),
      height: 50,
      textAlign: 'center',
      '& > img': {
        height: '100%',
        width: 'auto'
      }
    },
    content: {
      padding: theme.spacing(1, 2)
    },
    actions: {
      padding: theme.spacing(1, 2),
      display: 'flex',
      justifyContent: 'center'
    }
});

/**
 * Component to add/update prectice information
 */
class SetupPracticeStepTwo extends React.Component{

    constructor(props) {
        super(props);

        this.state = {
            connectAccountDetails: undefined
        }
    }

    /**
     * Get stripe connect account details of the user when component is render
     * AttachComponent method call when component is render
     */
    componentDidMount = () => {
        const { loggedInUserPractice } = this.props;
        if(loggedInUserPractice && loggedInUserPractice.PracticeId){
            this.props.getConnectAccount(loggedInUserPractice.PracticeId).then((res) => {
                if(res.accObj){
                    this.setState({
                        connectAccountDetails: res
                    })
                }
            });
        }
    }

    /**
     * Get stripe connect account details of the user when component receive new props
     * AttachComponent method call when component is render
     */
    componentWillReceiveProps = (nextProps) => {
        const { loggedInUserPractice } = nextProps;
        if(loggedInUserPractice && loggedInUserPractice.PracticeId){
            this.props.getConnectAccount(loggedInUserPractice.PracticeId).then((res) => {
                if(res.accObj){
                    this.setState({
                        connectAccountDetails: res
                    })
                }
            });
        }
    }

    render(){

        const { classes, className } = this.props;
        const { connectAccountDetails } = this.state;

        let parameters = {
            client_id: process.env.REACT_APP_STRIPE_CLIENT_ID,
            redirect_uri: process.env.REACT_APP_REDIRECT__URL_FOR_STRIPE_CONNECT_ACCOUNT,
            state: "fsdfsf",
            'stripe_user[business_type]': 'individual',
            // 'suggested_capabilities[]': 'card_payments',
            'suggested_capabilities[]': 'card_payments'
            // 'stripe_user[email]': loggedInUserPractice?loggedInUserPractice.Practice.email || undefined : undefined,
        };

        let url = `https://connect.stripe.com/express/oauth/authorize?${querystring.stringify(parameters)}`;

        return(
            <>
                <div>
                    {
                        connectAccountDetails
                        ?(
                            <Grid container spacing={3} className="margineTop50">
                                <Grid item xs={4}></Grid>
                                <Grid item xs={4}>
                                    <div className={`${clsx(classes.root, className)}`} >
                                        <div className={classes.media}>
                                            <img
                                            alt="Upgrade to PRO"
                                            src={ Stripe_Logo }
                                            />
                                        </div>
                                        <div className={classes.content}>
                                            <Typography
                                            align="center"
                                            gutterBottom
                                            variant="h6"
                                            >
                                            Connect Account
                                            </Typography>
                                            <Typography
                                            align="center"
                                            variant="body2"
                                            >
                                            Your business connected with stripe, please proceed to next step
                                            </Typography>
                                        </div>
                                        <div className={classes.actions}>
                                                <Button
                                                color="primary"
                                                onClick={this.props.nextCuttonClick}
                                                variant="contained"
                                                >
                                                    Next
                                                </Button>
                                        </div>
                                        <BottomNavigation value={"aboutStripe"} className={classes.root}>
                                            <BottomNavigationAction component="a" target="_blank" href="https://stripe.com/about" label="About stripe" value="aboutStripe" />
                                        </BottomNavigation>
                                    </div>
                                </Grid>
                                <Grid item xs={4}></Grid>
                                
                            </Grid>
                        )
                        :(
                            <Grid container spacing={3}>
                                <Grid item xs={12} className="informationHeader subscriptionTitle margineTop50">
                                    <img className="stripeImageHeightWidth" src={StripeLogo} alt="Stripe" />
                                </Grid>
                                <Grid item xs={12} className="informationHeader subscriptionTitle">
                                    <h4>
                                        Get paid by for every appointment you take!
                                    </h4>
                                </Grid>
                                <Grid item xs={3}>
                                </Grid>
                                <Grid item xs={6} className="informationHeader subscriptionTitle">
                                    <h5>
                                        Our platform has partnered with Stripe for Documentation for fast, secure payments. Fill out a few details so you can start getting paid.
                                    </h5>
                                </Grid>
                                <Grid item xs={3}>
                                </Grid>
                                <Grid item xs={12} className="informationHeader subscriptionTitle">
                                    <Button 
                                        size="large" variant="contained" color="primary" 
                                        component="a" href={ url }>
                                        Connect to stripe
                                    </Button>
                                </Grid>

                                <Grid item xs={12} className="informationHeader subscriptionTitle">
                                    <BottomNavigation value={"aboutStripe"} className={classes.root}>
                                        <BottomNavigationAction component="a" target="_blank" href="https://stripe.com/about" label="About stripe" value="aboutStripe" />
                                    </BottomNavigation>
                                </Grid>
                            </Grid>
                        )
                    }
                </div>

                <MessageModal
                    ref="messageModal"
                />
            </>
        )
    }

}

const mapStateToProps = (state) => {
    return {
        loggedInUser: state.auth.loggedInUser,
        currentRole: state.auth.currentRole,
        loggedInUserPractice: state.auth.loggedInUserPractice
    }
};

const mapDispatchToProps = (dispatch) => {
  return {
    getPracticeDetails: (practiceId) => {
      return dispatch(getPracticeDetailsRequest(practiceId)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },
    getConnectAccount: (id) => {
      return dispatch(getConnectAccountRequest(id)).then((res) => {
      return Promise.resolve(res);
      }, (error) => {
      return Promise.reject(error);
      });
    }
  }
};

SetupPracticeStepTwo = connect(
  mapStateToProps,
  mapDispatchToProps
)(SetupPracticeStepTwo);

export default withStyles(styles)(SetupPracticeStepTwo);
