import React from 'react';
import './SetupPractice.css';
import {connect} from 'react-redux';
import {Redirect} from "react-router-dom";

import {withStyles} from '@material-ui/core/styles';
import {
  Grid, Stepper, Step, StepLabel, Snackbar, Button
} from '@material-ui/core';

import Topbar from '../../../../utils/components/Topbar/Topbar';

import {setPageTitle} from "../../../Dashboard/DashboardActions";
import SetupPracticeStepOne from './SetupPracticeStepOne';
import SetupPracticeStepTwo from './SetupPracticeStepTwo';
import SetupPracticeStepThree from './SetupPracticeStepThree';
import SetupPracticeStepFour from './SetupPracticeStepFour';

import {getUserPracticeRequest} from '../../../User/UserApiActions';
import AppLoader from "../../../../utils/components/AppLoader/AppLoader";
import {Alert} from "@material-ui/lab";
import {clearSnackbar} from "../../../../utils/components/AppLoader/AppLoaderActions";

const styles = theme => ({
  root: {
    backgroundColor: '#fff',
    paddingTop: '48px',
    paddingLeft: '20px',
    paddingRight: '20px',
    width: '100%',
    height: '100%'
  },
  grid: {},
  stepperRoot: {
    width: '100%',
  },
  backButton: {
    marginRight: theme.spacing(1),
  },
  instructions: {},
});

let getSteps = ['Fillup Information', 'Add User'];

/**
 * Login page shown to the user when session is not present
 */
class SetupPractice extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      activeStep: 0,
      allStepsDone: false,
    };
  }

  /**
   * Set page title action call when component is render
   * Set redirect url in session stotage when component is render
   * Get user practice details when component is render
   * AttachComponent method call when component is render
   */
  componentDidMount = () => {
    const { loggedInUser } = Object.assign({}, this.props);
    sessionStorage.setItem('redirectUrl', '/setupPractice');
    this.props.updatePageTitle("Setup Practice", false);
    this.props.getUserRequest(loggedInUser.id).then((res)=>{
      if(res){
        let activeStep = this.getActiveStep(res, loggedInUser);
        this.setState({ activeStep });
      }
    })
  }

  /**
   * Set page title action call when component is render
   * AttachComponent method call when component is render
   */
  componentDidUpdate = () => {
    this.props.updatePageTitle("Setup Practice", false);
  }

  /**
   * AttachComponent method call when component is recive new props
   */
  componentWillReceiveProps = (nextProps) => {
    // const { loggedInUserPractice } = Object.assign({}, nextProps);

    // let activeStep = this.getActiveStep(loggedInUserPractice, loggedInUser);
    // this.setState({ activeStep })
  }

  /**
   * Method is used to get index number of step using step label
   */
  getStepsIndex = (stepName) => {
    return getSteps.findIndex((step) => step === stepName);
  }

  /**
   * Method is used to get active steps
   */
  getActiveStep = (loggedInUserPractice, loggedInUser) => {
    return !loggedInUserPractice.PracticeId?this.getStepsIndex("Fillup Information"):(loggedInUserPractice.Practice.allowPayment && !loggedInUserPractice.Practice.stripeConnectAccountId)?this.getStepsIndex("Verify Business"):(loggedInUser.isSubscription && !loggedInUserPractice.Practice.stripeSubscriptionId)?this.getStepsIndex("Subscription"):this.getStepsIndex("Add User");
  }

  /**
   * Method executes for go to next steps
   */
  next = () => {
    const activeStep = this.state.activeStep + 1;
    this.setState({ activeStep });
  }

  /**
   * Method executes for go to previous steps
   */
  prev = () => {
    const activeStep = this.state.activeStep - 1;
    this.setState({ activeStep });
  }

  /**
   * Method executes for set all steps done
   */
  finishSetup = () => {
    this.setState({
      allStepsDone: true
    })
  }

  /**
   * Method executes for render the active steps
   */
  stepRender = (activeStep) => {
    switch(getSteps[activeStep]) {
      case 'Fillup Information':
        return <SetupPracticeStepOne nextButtonClick={() => this.next()} prevButtonClick={() => this.prev()} />;
      case 'Verify Business':
        return <SetupPracticeStepTwo nextCuttonClick={() => this.next()} prevButtonClick={() => this.prev()} />;
      case 'Subscription':
        return <SetupPracticeStepThree nextButtonClick={() => this.next()} prevButtonClick={() => this.prev()} />;
      case 'Add User':
        return <SetupPracticeStepFour finishSetup={() => this.finishSetup()} prevButtonClick={() => this.prev()} />;
      default:
        return console.log("Outside Stepper");
    }
  }

  /**
   * Method used for rendering the steps based on logged in user details and logged in user practice details params
   */
  setupPracticeRender = () => {
    const { classes, loggedInUser, currentRole, loggedInUserPractice } = Object.assign({}, this.props);
    const { activeStep, allStepsDone } = Object.assign({}, this.state);
    const isFindSubscriptionPosition = getSteps.find((step) => step === "Subscription");
    const isFindStripeConnectAccountPosition = getSteps.find((step) => step === "Verify Business");
    if(loggedInUser.isSubscription && !isFindSubscriptionPosition){
      getSteps.splice(getSteps.length-1, 0, 'Subscription');
    }else{
      if(!loggedInUser.isSubscription && isFindSubscriptionPosition){
        getSteps.splice(getSteps.findIndex((step) => step === isFindSubscriptionPosition), 1);
      }
    } 
    if(loggedInUserPractice && loggedInUserPractice.PracticeId && loggedInUserPractice.Practice.allowPayment && !isFindStripeConnectAccountPosition){
      getSteps.splice(getSteps.length - (getSteps.length-1), 0, 'Verify Business');
    }else{
      if(loggedInUserPractice && loggedInUserPractice.PracticeId && !loggedInUserPractice.Practice.allowPayment && isFindStripeConnectAccountPosition){
        getSteps.splice(getSteps.findIndex((step) => step === isFindStripeConnectAccountPosition), 1);
      }
    }
    
    const steps = getSteps;
    
    if(!loggedInUser){
      return (<Redirect to="/signin"/>);
    }else if(loggedInUser && !currentRole.isPracticeAdmin){
      return (<Redirect to="/"/>);
    }else if(currentRole.isPracticeAdmin && loggedInUserPractice && loggedInUserPractice.PracticeId && allStepsDone){
      return (<Redirect to="/dashboard"/>);
    }else{
      return (
        <>
          <Topbar pageTitle="Setup Practice" activeStep={activeStep} prevButtonClick = { () => this.prev() }/>
          <div className={classes.root}>
            <Grid
              className={classes.grid}
              container
            >
              <Stepper className={classes.stepperRoot}  activeStep={activeStep} alternativeLabel>
                {steps.map((step) => (
                  <Step key={step}>
                    <StepLabel>{step}</StepLabel>
                  </Step>
                ))}
              </Stepper>
            </Grid>

            <div className="stepperContainer">
              {
                this.stepRender(activeStep)
              }
            </div>

            {
              activeStep > 0
              ?(
                <Button className="prevButtonStyle" size="small" color="primary" variant="contained" onClick={this.prev}>
                  prev
                </Button>
              )
              :<></>
            }
          </div>

          <Snackbar open={this.props.showSnackbar} autoHideDuration={3000}
                    onClose={() => this.handleCloseSnackbar()}
                    anchorOrigin={{vertical: 'top', horizontal: 'right'}}>
            <Alert severity={this.props.snackbarVariant}>
              {this.props.snackbarMessage}
            </Alert>
          </Snackbar>

          <AppLoader showLoader={this.state.showLoader}/>
        </>
      );
    }
  }

  /**
   * Method used to close the snackbar
   */
  handleCloseSnackbar = () => {
    this.props.clearSnackbar();
  };


  /**
   * Render the UI of the setup practice form
   * @returns {XML}
   */
  render() {
    document.title = 'CARE';

    return this.setupPracticeRender();
  }
}

const mapStateToProps = state => {
  return {
    loggedInUser: state.auth.loggedInUser,
    currentRole: state.auth.currentRole,
    loggedInUserPractice: state.auth.loggedInUserPractice,
    showSnackbar: state.showLoader.showSnackbar,
    snackbarVariant: state.showLoader.snackbarVariant,
    snackbarMessage: state.showLoader.snackbarMessage
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    updatePageTitle: (pageTitle, enablePageTitle) => {
      dispatch(setPageTitle(pageTitle, enablePageTitle));
    },
    getUserRequest: (id) => {
      return dispatch(getUserPracticeRequest(id)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },
    clearSnackbar: () => {
      dispatch(clearSnackbar())
    },
  }
};

SetupPractice = connect(
  mapStateToProps,
  mapDispatchToProps
)(SetupPractice);

export default withStyles(styles)(SetupPractice);