import React from 'react';
import {CardNumberElement, CardExpiryElement, CardCvcElement, injectStripe} from 'react-stripe-elements';
import {
    Button, Grid
} from '@material-ui/core';
import Alert from '@material-ui/lab/Alert';
import { ValidatorForm, TextValidator} from 'react-material-ui-form-validator';
import {ONLY_BLANK_SPACES} from '../../../../utils/Constants';
import UtilHelper from '../../../../utils/UtilHelper';

import Constants from '../../../../utils/Constants';
import ConstantMessages from '../../../../utils/Strings';

const createOptions = () => {
    return {
      style: {
        base: {
          fontSize: '16px',
          margin:"5px",
          color: '#424770',
          fontFamily: 'Open Sans, sans-serif',
          letterSpacing: '0.025em',
          '::placeholder': {
            color: '#aab7c4',
          },
        },
        invalid: {
          color: '#c23d4b',
        },
      }
    }
};

function StripeInput(props) {
    const { component: Component, inputRef, ...other } = props;
    const elementRef = React.useRef();
  
    React.useImperativeHandle(inputRef, () => ({
      focus: () => elementRef.current.focus
    }));
  
    return (
      <Component onReady={element => (elementRef.current = element)} {...other} />
    );
}

class BillingForm extends React.Component{
    
    constructor(){
        super();
        this.state = {
            value: {
                cardHolderName: "",
                cardNumber: "",
                cardExpiry: "",
                cardCvc: ""
            },
            errorMessageObj: {
                cardNumber: "",
                cardExpiry: "",
                cardCvc: ""
            },
            isError: false,
            errorMessage: ""
        };
    }

    /**
     * Add custom validation rule in ValidatorForm when component is render
     * AttachComponent method call when component is render
     */
    componentDidMount = () => {
        ValidatorForm.addValidationRule(ONLY_BLANK_SPACES, UtilHelper.validateBlankSpaces);
    };

    /**
     * Remove custom validation rule in ValidatorForm when component is unmount
     * AttachComponent method call when component is unmount
     */
    componentWillUnmount() {
        ValidatorForm.removeValidationRule(ONLY_BLANK_SPACES);
    }

    /**
     * Method executes on onChange action of form element
     */
    handleChange = (data) => {
        let { errorMessageObj } = this.state;
        if(data.error){
            errorMessageObj[data.elementType] = data.error.message;
            this.setState({ errorMessageObj, isError:true, errorMessage: data.error.message }, () => {
                
            })
        }else{
            errorMessageObj[data.elementType] = '';
            this.setState({ errorMessageObj, isError:false, errorMessage: '' }, () => {
                
            })
        }
    };

    /**
     * Method executes on onChange action of card holder name form element
     */
    handleChangeName = (name) => {
        let { value } = this.state;
        value.cardHolderName = name

        this.setState({
            value
        })
    }

    /**
     * Method executes on submit form action
     */
    handleSubmit = (evt) => {
      evt.preventDefault();
    
      if (this.props.stripe) {
        this.props.showLoader();
        this.props.stripe.createToken().then((res)=>{
            if(res.error){
                this.setState({
                    isError:true,
                    errorMessage: res.error.message
                }, () => {
                    this.props.hideLoader();
                })
            }else{
                this.setState({
                    isError:false,
                    errorMessage: ''
                },()=>{
                    this.props.handleTokenCreation(res);
                })
                
            }
        });
      }
    };

    render(){
        const { value } = this.state;
        return(
            <ValidatorForm
                  ref="form"
                  onSubmit={this.handleSubmit.bind(this)}
                  noValidate
                  onError={errors => console.log(errors)}
                >
            
                <Grid container spacing={0}>
                    {
                        this.state.isError
                        ?(
                            <Grid item xs={12}>
                            <Alert severity="error">{ this.state.errorMessage }</Alert>
                            </Grid>
                        )
                        :<></>
                    }
                    <Grid item xs={12}>
                        <div className="formGroupContainer">
                            <TextValidator
                                fullWidth
                                variant="outlined"
                                required
                                InputLabelProps={{
                                    shrink: true,
                                }}
                                placeholder="Name On Credit Card"
                                label="Name On Credit Card" onChange={(event) => this.handleChangeName(event.target.value, "", "name")}
                                name="cardHolderName" value={value.cardHolderName || ''} 
                                validators={['required', ONLY_BLANK_SPACES, 'maxStringLength:' + Constants.VALIDATIONS.MAXIMUM_CHARACTERS_TEXT_FIELD]}
                                errorMessages={['this field is required', Constants.ERROR_MESSAGES.blank_spaces_not_allowed, ConstantMessages.maximum_allowed_characters_for_text_field]}
                            />
                        </div>
                    </Grid>
                    <Grid item xs={12}>
                        <div className="formGroupContainer">
                            <TextValidator
                                fullWidth
                                variant="outlined"
                                required
                                helperText={this.state.errorMessage.cardNumber ? this.state.errorMessage.cardNumber || "Invalid" : ""}
                                InputProps={{
                                    inputProps: {
                                      component: CardNumberElement
                                    },
                                    inputComponent: StripeInput,
                                }}
                                InputLabelProps={{
                                    shrink: true,
                                }}
                                label="Card Number" 
                                onChange={this.handleChange}
                            />
                        </div>
                    </Grid>
                    <Grid item xs={6}>
                        <div className="formGroupContainer">
                            <TextValidator
                                fullWidth
                                variant="outlined"
                                required
                                helperText={this.state.errorMessage.cardNumber ? this.state.errorMessage.cardNumber || "Invalid" : ""}
                                InputProps={{
                                    inputProps: {
                                      component: CardExpiryElement
                                    },
                                    inputComponent: StripeInput,
                                }}
                                InputLabelProps={{
                                    shrink: true,
                                }}
                                label="Expiration Date" 
                                onChange={this.handleChange}
                            />
                        </div>
                    </Grid>
                    <Grid item xs={6}>
                        <div className="formGroupContainer">
                            <TextValidator
                                fullWidth
                                variant="outlined"
                                InputProps={{
                                    inputProps: {
                                      component: CardCvcElement
                                    },
                                    inputComponent: StripeInput,
                                }}
                                InputLabelProps={{
                                    shrink: true,
                                }}
                                label="CVC" 
                                onChange={this.handleChange}
                            />
                        </div>
                    </Grid>
                </Grid>

                <div className="formGroupContainer">
                <Button type="submit" variant="contained" color="primary" className="pay-button">Purchase Subscription</Button>
                </div>
            </ValidatorForm>
        )
    }

}

export default injectStripe(BillingForm);

