import React, {Component} from 'react';
import './ConsultHistory.css';
import {AppBar, Button, Snackbar, Toolbar, Typography} from "@material-ui/core";
import {downloadConsultFile, getConsultFiles, getConsultInfo, getConsultNotes} from "../../ConsultApiActions";
import {connect} from "react-redux";
import MessageModal from "../../../../utils/components/MessageModal/MessageModal";
import {Redirect} from "react-router";
import Constants from '../../../../utils/Constants';
import ConstantMessages from '../../../../utils/Strings';
import {clearSnackbar, showSnackbar} from "../../../../utils/components/AppLoader/AppLoaderActions";
import {Alert} from "@material-ui/lab";
import Card from "@material-ui/core/Card";
import Grid from "@material-ui/core/Grid";
import moment from 'moment';
import SingleFile from "../Document/SingleFile";
import {Modal} from 'antd';
import UtilHelper from "../../../../utils/UtilHelper";

function getBase64(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = error => reject(error);
  });
}

class ConsultHistory extends Component {
  constructor(props) {
    super(props);

    this.state = {
      consult: {},
      soapNote: null,
      files: null,
      previewVisible: false,
      previewImage: null,
    };
  }

  componentDidMount() {
    this.getConsultInformation();
    this.getSoapNote();
    this.getDocuments();
  }

  getConsultInformation = () => {
    const consultId = sessionStorage.getItem('consultHistory');
    if (consultId != null) {
      this.props.getConsultInfo(consultId).then(res => {
        this.setState({consult: res.data[0]});
      }, error => {
        this.props.showSnackbar(ConstantMessages.somethingWentWrong, Constants.SNACKBAR_VARIAINT.ERROR);
      });
    }
  };

  getSoapNote = () => {
    const consultId = sessionStorage.getItem('consultHistory');
    if (consultId != null) {
      this.props.getSoapNote(consultId).then(res => {
        this.setState({soapNote: res.data});
      }, error => {
        this.props.showSnackbar(ConstantMessages.somethingWentWrong, Constants.SNACKBAR_VARIAINT.ERROR);
      });
    }
  };

  getDocuments = () => {
    const consultId = sessionStorage.getItem('consultHistory');
    if (consultId != null) {
      this.props.getDocuments(consultId).then(res => {
        this.setState({files: res.data});
      }, error => {
        this.props.showSnackbar(ConstantMessages.somethingWentWrong, Constants.SNACKBAR_VARIAINT.ERROR);
      });
    }
  };

  handlePreview = fileId => {
    const previewImage = process.env.REACT_APP_CONSULT_SERVER_API_URL + `/consult/consultFiles/${fileId}`;
    this.setState({previewVisible: true, previewImage: previewImage});
  };

  /**
   * Method used to close the snackbar
   */
  handleCloseSnackbar = () => {
    this.props.clearSnackbar();
  };

  handleCancel = () => {
    this.setState({previewVisible: false, previewImage: ''});
  };

  render() {
    const consultId = sessionStorage.getItem('consultHistory');
    const {consult, soapNote, files, previewVisible, previewImage} = this.state;

    if (consultId === null) {
      return <Redirect to={'/dashboard'}/>
    }
    return (
      <>
        <AppBar
          position="fixed">
          <Toolbar>
            <Typography variant="h6" noWrap className="sideMenuName">
              Historical Consult
            </Typography>
            <Button variant="contained" color="secondary" className={'end-consult-button'}
                    onClick={() => this.props.history.goBack()}>Close</Button>
          </Toolbar>
        </AppBar>
        <main className={'history-consult-content'}>
          {
            consult.patientDetails &&
            <Card className={'information-card'}>
              <div className={'section-title'}>Patient Information</div>
              <Grid container spacing={3}>
                <Grid item xs={12} sm={6}>
                  <div><b>Patient Name:</b> {consult.patientDetails.name}</div>
                </Grid>
                <Grid item xs={12} sm={6}>
                  <div><b>Appointment Time:</b> {consult.appointmentTime}</div>
                </Grid>
                <Grid item xs={12} sm={6}>
                  <div><b>Start Time:</b> {moment(consult.consultStartTime).format('DD-MM-YYYY hh:mm a z')}</div>
                </Grid>
                <Grid item xs={12} sm={6}>
                  <div><b>End Time:</b> {moment(consult.consultEndTime).format('DD-MM-YYYY hh:mm a z')}</div>
                </Grid>
                <Grid item xs={12} sm={12}>
                  <div><b>Chief Complaint:</b> {consult.patientDetails.chiefComplaint}</div>
                </Grid>
              </Grid>
            </Card>
          }

          {
            soapNote ?
              (<Card className={'information-card'}>
                <div className={'section-title'}>Notes</div>
                <Grid container spacing={3}>
                  {/* <Grid item xs={12}>
                    <div><b>Subjective:</b> {soapNote.subjective}</div>
                  </Grid>
                  <Grid item xs={12}>
                    <div><b>Objective:</b> {soapNote.objective}</div>
                  </Grid>
                  <Grid item xs={12}>
                    <div><b>Assessment:</b> {soapNote.assessment}</div>
                  </Grid> */}
                  <Grid item xs={12}>
                    <div>{soapNote.plan}</div>
                  </Grid>
                </Grid>
              </Card>)
              :
              (<Card className={'information-card'}>
                <div className={'section-title'}>Notes</div>
                <div>Notes not available</div>
              </Card>)
          }

          {
            <Card className={'information-card'}>
              <div className={'section-title'}>Documents</div>
              <div>
                {files && files.map(file => (
                  <SingleFile key={file.fileId} file={file} handleRemove={this.handleRemove}
                              handlePreview={this.handlePreview} userId={null}/>
                ))}
                {
                  files && files.length === 0 &&
                    <div>Documents not available</div>
                }
              </div>
            </Card>
          }

          <Card className={'information-card'}>
            <div className={'section-title'}>Recordings</div>
            {
              consult.recordings && consult.recordings.map(recording => (
                <video key={recording.id} width="320" height="240" controls src={recording.url} className={'video-recording'}/>
              ))
            }
          </Card>

          <Snackbar open={this.props.showSnackbar} autoHideDuration={3000}
                    onClose={() => this.handleCloseSnackbar()}
                    anchorOrigin={{vertical: 'top', horizontal: 'right'}}>
            <Alert severity={this.props.snackbarVariant}>
              {this.props.snackbarMessage}
            </Alert>
          </Snackbar>

          <Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
            <img alt="example" style={{ width: '100%' }} src={previewImage} />
          </Modal>
        </main>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    showSnackbar: state.showLoader.showSnackbar,
    snackbarVariant: state.showLoader.snackbarVariant,
    snackbarMessage: state.showLoader.snackbarMessage
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    getConsultInfo: consultId => {
      return dispatch(getConsultInfo(consultId)).then(res => {
        return Promise.resolve(res);
      }, error => {
        return Promise.reject(error);
      });
    },

    getSoapNote: consultId => {
      return dispatch(getConsultNotes(consultId)).then(res => {
        return Promise.resolve(res);
      }, error => {
        return Promise.reject(error);
      });
    },
    getDocuments: consultId => {
      return dispatch(getConsultFiles(consultId)).then(res => {
        return Promise.resolve(res);
      }, error => {
        return Promise.reject(error);
      });
    },
    downloadConsultFile: consultId => {
      return dispatch(downloadConsultFile(consultId)).then(res => {
        return Promise.resolve(res);
      }, error => {
        return Promise.reject(error);
      });
    },
    showSnackbar: (message, variant) => {
      dispatch(showSnackbar(message, variant));
    },
    clearSnackbar: () => {
      dispatch(clearSnackbar())
    },
  }
};

ConsultHistory = connect(
  mapStateToProps,
  mapDispatchToProps
)(ConsultHistory);

export default ConsultHistory;
