import React, {Component} from 'react';
import {Upload, Modal, Icon} from 'antd';
import ConstantMessages from "../../../../utils/Strings";
import {getConsultFiles, updateConsultNote, uploadConsultFile, deleteConsultFile, downloadConsultFile} from "../../ConsultApiActions";
import {connect} from "react-redux";
import './Document.css';

import Constants from '../../../../utils/Constants';
import {showSnackbar} from "../../../../utils/components/AppLoader/AppLoaderActions";
import SingleFile from "./SingleFile";

class Document extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      previewVisible: false,
      previewImage: '',
      files: [],
    };
  }

  componentDidMount() {
    this.getConsultFiles();
  }

  getConsultFiles = () => {
    this.props.getConsultFiles(this.props.consultId)
      .then(res => {
        let files = [];

        res.data.forEach(file => {
          files.push({
            name: file.name,
            fileId: file.id,
            uid: file.id,
            fileType: file.fileType
          });
        });

        this.setState({files});
      }, error => {
        this.props.showSnackbar(ConstantMessages.unableToGetConsultDocuments, Constants.SNACKBAR_VARIAINT.ERROR);
      });
  };

  handleCancel = () => this.setState({ previewVisible: false });

  handlePreview = async fileId => {
    const previewImage = process.env.REACT_APP_CONSULT_SERVER_API_URL + `/consult/consultFiles/${fileId}`;
    this.setState({previewVisible: true, previewImage: previewImage});
  };

  handleCancel = () => {
    this.setState({previewVisible: false, previewImage: ''});
  };

  handleRemove = async fileId => {
    console.log(fileId);
    this.props.deleteConsultFile(fileId)
      .then(res => {
        let files = this.state.files.filter(file => file.fileId !== fileId);
        this.setState({files});
      }, error => {
        this.props.showSnackbar(ConstantMessages.unableToDeleteFile, Constants.SNACKBAR_VARIAINT.ERROR);
      });
  };

  beforeUpload = (file) => {
    let allowedFiles = ['image/jpeg', 'image/jpg', 'image/png', 'application/pdf'];

    let isImageFile = allowedFiles.indexOf(file.type) >= 0;
    if (!isImageFile) {
      this.props.showSnackbar(ConstantMessages.incorrectFileFormat, Constants.SNACKBAR_VARIAINT.ERROR);
    } else {
      const formData = new FormData();
      formData.append('file', file);
      formData.append('consultId', this.props.consultId);

      this.props.uploadConsultFile(formData, this.props.consultId).then(res => {
        this.props.showSnackbar(ConstantMessages.imageUploadSuccessful, Constants.SNACKBAR_VARIAINT.INFO);
        file.fileId = res.fileId;
        this.setState({files: this.state.files.concat(file)});
      }, error => {
        this.props.showSnackbar(ConstantMessages.imageUploadFailed, Constants.SNACKBAR_VARIAINT.ERROR);
      });

    }
    return false;
  };

  render() {
    const {previewVisible, previewImage, files} = this.state;
    const {loggedInUser} = this.props;

    return (
      <div className="clearfix">
        <div>
          {files && files.map(file => (
            <SingleFile key={file.fileId} file={file} handleRemove={this.handleRemove} handlePreview={this.handlePreview} userId={loggedInUser.id}/>
          ))}
          <Upload
            key={'uploadFile'}
            name="avatar"
            listType="picture-card"
            className="avatar-uploader"
            showUploadList={false}
            beforeUpload={this.beforeUpload}>
            <div>
              <Icon type={'plus'} />
              <div className="ant-upload-text">Upload</div>
            </div>
          </Upload>
        </div>

        <Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
          <img alt="example" style={{ width: '100%' }} src={previewImage} />
        </Modal>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    loggedInUser: state.auth.loggedInUser,
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    uploadConsultFile: (data, consultId) => {
      return dispatch(uploadConsultFile(data, consultId)).then(res => {
        return Promise.resolve(res);
      }, error => {
        return Promise.reject(error);
      });
    },
    deleteConsultFile: consultId => {
      return dispatch(deleteConsultFile(consultId)).then(res => {
        return Promise.resolve(res);
      }, error => {
        return Promise.reject(error);
      });
    },
    downloadConsultFile: consultId => {
      return dispatch(downloadConsultFile(consultId)).then(res => {
        return Promise.resolve(res);
      }, error => {
        return Promise.reject(error);
      });
    },
    getConsultFiles: consultId => {
      return dispatch(getConsultFiles(consultId)).then(res => {
        return Promise.resolve(res);
      }, error => {
        return Promise.reject(error);
      });
    },
    showSnackbar: (message, variant) => {
      dispatch(showSnackbar(message, variant));
    }
  }
};

Document = connect(
  mapStateToProps,
  mapDispatchToProps
)(Document);

export default Document;
