import HttpRequest from '../../utils/PracticeHttpRequest';
import HttpRequestGeoCode from '../../utils/HttpRequestGeoCode';

import {SNACKBAR_VARIAINT} from '../../utils/Constants';
import {hideLoader, showLoader, showSnackbar} from '../../utils/components/AppLoader/AppLoaderActions'

import {
  // createPractice,
  // setupPractice,
  // updatePractice
} from "./PracticeActions";

import { setPracticeInLoggedInUser } from '../Authentication/AuthenticationActions';
import { getUserPracticeRequest } from '../User/UserApiActions';
import ConstantMessages from '../../utils/Strings';

/**
 * Server API call to create a new practive
 * - Dispatch setPracticeInLoggedInUser action after successful response from server
 * @returns {function(*)}
 */
export function setupPracticeRequest(data) {
  return (dispatch) => {
    dispatch(showLoader());
    return new HttpRequest(dispatch, '/practice', 'post', data)
      .send()
      .then(res => {
        dispatch(showSnackbar(ConstantMessages.practiceInformationSaveSuccessMessage, SNACKBAR_VARIAINT.SUCCESS));
        dispatch(getUserPracticeRequest(res.data.data.practiceOwner.userId))
        dispatch(setPracticeInLoggedInUser(res.data.data));
        dispatch(hideLoader());
        return Promise.resolve(res.data.data);
      }, error => {
        dispatch(hideLoader());
        return Promise.reject(error);
      });
  };
}

/**
 * Server API call to update an practive
 * - Dispatch setPracticeInLoggedInUser action after successful response from server
 * @returns {function(*)}
 */
export function updatePracticeRequest(practiceId, data) {
  return (dispatch) => {
    dispatch(showLoader());
    return new HttpRequest(dispatch, `/practice/${practiceId}`, 'patch', data)
      .send()
      .then(res => {
        dispatch(showSnackbar(ConstantMessages.practiceInformationSaveSuccessMessage, SNACKBAR_VARIAINT.SUCCESS));
        dispatch(getUserPracticeRequest(res.data.data.practiceOwner.userId));
        dispatch(setPracticeInLoggedInUser(res.data.data));
        dispatch(hideLoader());
        return Promise.resolve(res.data.data);
      }).catch((error) => {
        dispatch(hideLoader());
        return Promise.reject(error);
      });
  };
}

/**
 * Server API call to fetch an practice details of logged in user
 * - Dispatch setPracticeInLoggedInUser action after successful response from server
 * @returns {function(*)}
 */
export function getPracticeDetailsRequest(practiceId) {
  return (dispatch) => {
    dispatch(showLoader());
    return new HttpRequest(dispatch, `/practice/${practiceId}`, 'get', {})
      .send()
      .then(res => {
        dispatch(setPracticeInLoggedInUser(res.data.data));
        dispatch(hideLoader());
        return Promise.resolve(res.data.data);
      }).catch((error) => {
        dispatch(hideLoader());
        return Promise.reject(error);
      });
  };
}

/**
 * Server API call to fetch an user coordinates details based on practice pincode
 * @returns {function(*)}
 */
export function getCoordinatesRequest(pincode) {
  let urlParams = `address=${pincode}&key=AIzaSyBMslEWu50oC_tm2dGRy8RrDPyftvdqpj0`;
  return (dispatch) => {
    dispatch(showLoader());
    return new HttpRequestGeoCode(dispatch, `/geocode/json?${urlParams}`, 'get', {})
      .send()
      .then(res => {
        dispatch(hideLoader());
        return Promise.resolve(res.data.results);
      }).catch((error) => {
        dispatch(hideLoader());
        return Promise.reject(error);
      });
  };
}