import {PAGE_SECONDARY_TITLE, PAGE_TITLE, SHOW_SIDE_MENU, TOGGLE_SIDE_MENU, DASHABOARD_STATS} from './DashboardActions';
import {SIGN_OUT} from "../Authentication/AuthenticationActions";

const initialState = {pageTitle: null, secondaryTitle: null, showSideMenu: true, enableMenuToggle: false};

/**
 * Reducer to update the state of the application
 * @param state: Current state value
 * @param action: action received
 * @returns {*} current updated state value
 * @constructor
 */
const DashboardReducer = (state = initialState, action) => {

  switch (action.type) {

    // Store the page title, enable menu toggle, shoe side menu
    case PAGE_TITLE :
      return Object.assign({}, state, {
        pageTitle: action.pageTitle,
        secondaryTitle: null,
        enableMenuToggle: action.enableMenuToggle,
        showSideMenu: action.enableMenuToggle
      });

    // Store secondary page title
    case PAGE_SECONDARY_TITLE :
      return Object.assign({}, state, {
        pageTitle: state.pageTitle,
        secondaryTitle: action.secondaryTitle,
      });

    // Store data for toggle side menu
    case TOGGLE_SIDE_MENU:
      return Object.assign({}, state, {
        showSideMenu: !state.showSideMenu
      });

    // Side menu show/hide
    case SHOW_SIDE_MENU:
      return Object.assign({}, state, {
        showSideMenu: true
      });

    // Store dashboard stats
    case DASHABOARD_STATS:
      return Object.assign({}, state, {
        stats: action.stats
      });

    case SIGN_OUT:
      return Object.assign({}, initialState);

    default:
      return state;
  }
};

export default DashboardReducer;
