import React from 'react';
import './UserDashboard.css';
import {connect} from 'react-redux';

import {withStyles} from '@material-ui/core/styles';
import { Grid } from '@material-ui/core';

import ConsultsCard from './component/ConsultsCard';
import RequestQueueConsults from './component/RequestQueueConsults';
import UpcomingConsults from './component/UpcomingConsults';
import {setPageTitle} from "../../DashboardActions";

import UserTable from '../../../User/components/UserTable/UserTable';

import Constants from '../../../../utils/Constants';

const styles = theme => ({
  root: {
    padding: theme.spacing(4)
  }
});
class UserDashboard extends React.Component {

  constructor(){
    super();
    this.state= {
      isRefreshCount : false
    }
  }

  /**
   * Method is used to get new dashboard stats
   */
  callBackCounts = () => {
    this.setState({
      isRefreshCount: !this.state.isRefreshCount
    })
  }

  /**
   * Set page title action call
   * AttachComponent method call when component is render
   */
  componentDidMount = () => {
    this.props.updatePageTitle("Dashboard", false);
  }

  render() {
    let isDoctor = this.props.loggedInUser.Roles.find((role) => role.name===Constants.SYSTEM_ROLES.ROLE_DOCTOR);
    let isCoordinator = this.props.loggedInUser.Roles.find((role) => role.name===Constants.SYSTEM_ROLES.ROLE_COORDINATOR);
    return (
      <div>
        <Grid container spacing={4}>
          <Grid item xs={12}>
            <ConsultsCard isRefreshCount={this.state.isRefreshCount} callBackCounts={this.callBackCounts} />
          </Grid>
          {
            (isDoctor || isCoordinator)
            ?(
              <>
                <Grid item xs={12}>
                  <RequestQueueConsults callBackCounts={this.callBackCounts} />
                </Grid>
                <Grid item xs={12}>
                  <UpcomingConsults />
                </Grid>
              </>
            )
            :(
              <Grid item xs={12}>
                <UserTable />
              </Grid>
            )
          }
        </Grid>
      </div>
    )
  }
}

const mapStateToProps = state => {
  return {
    loggedInUser: state.auth.loggedInUser,
    currentRole: state.auth.currentRole
  }
};

const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    updatePageTitle: (pageTitle, enablePageTitle) => {
      dispatch(setPageTitle(pageTitle, enablePageTitle));
    },
  }
}

UserDashboard = connect(
  mapStateToProps,
  mapDispatchToProps
)(UserDashboard);

export default withStyles(styles)(UserDashboard);
