// Export Constants (Action names)
export const SUBSCRIPTION_PLANS = 'SUBSCRIPTION_PLANS';

/**
 * Action called after get all subscription plans api call
 * @param subscriptionPlans
 */
export function getAllSubscriptionPlans(subscriptionPlans) {

  return {
    type: SUBSCRIPTION_PLANS,
    subscriptionPlans
  };
}
