import React from 'react';
import {connect} from 'react-redux';
import clsx from 'clsx';
import Stripe_Logo from '../../../assets/Stripe_Logo.png';
import {withStyles} from '@material-ui/core/styles';
import { Typography, Button, Tooltip } from '@material-ui/core';
import { getConnectAccountRequest } from "../SubscriptionPlansApiActions";

import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';

const styles = theme => ({
    root: {
        backgroundColor: "#fff",
        boxShadow: "0 0 0 1px rgba(63,63,68,0.05), 0 1px 3px 0 rgba(63,63,68,0.15)",
        borderRadius: '4px'
    },
    media: {
      paddingTop: theme.spacing(2),
      height: 50,
      textAlign: 'center',
      '& > img': {
        height: '100%',
        width: 'auto'
      }
    },
    content: {
      padding: theme.spacing(1, 2)
    },
    actions: {
      padding: theme.spacing(1, 2),
      display: 'flex',
      justifyContent: 'center'
    }
});

class StripeDashboard extends React.Component{

    constructor(props) {
        super(props);
        this.state = {
            connectAccountDetails: undefined
        }
    }

    /**
     * Get stripe connect account details of the logged in user when component is render
     * AttachComponent method call when component is render
     */
    componentDidMount = () => {
        const { loggedInUserPractice } = Object.assign({}, this.props) ;
        if(loggedInUserPractice){
            if(loggedInUserPractice.Practice.allowPayment && loggedInUserPractice.Practice.stripeConnectAccountId){
                this.props.getConnectAccount(loggedInUserPractice.PracticeId).then((res) => {
                    if(res.accObj){
                        this.setState({
                            connectAccountDetails: res
                        })
                    }
                });
            }
        }
    }
    
    render(){
        const { classes, className } = Object.assign({}, this.props);
        const { connectAccountDetails } = Object.assign({}, this.state);

        return (
            <div className={`${clsx(classes.root, className)}`} >
                <Tooltip placement="left" title={connectAccountDetails && connectAccountDetails.accObj.charges_enabled && connectAccountDetails.accObj.payouts_enabled? "Varified":"Not Varified"}>
                    <span className="parentClass">
                        <span class={`isVarified ${connectAccountDetails && connectAccountDetails.accObj.charges_enabled && connectAccountDetails.accObj.payouts_enabled? "bgColorGreen":"bgColorRed"}`}></span>
                    </span>
                    
                </Tooltip>
                <div className={classes.media}>
                    <img
                    alt="Upgrade to PRO"
                    src={ Stripe_Logo }
                    />
                </div>
                <div className={classes.content}>
                    <Typography
                    align="center"
                    gutterBottom
                    variant="h6"
                    >
                    Connect Account
                    </Typography>
                    <Typography
                    align="center"
                    variant="body2"
                    >
                    To update your business account details. Please click on below button.
                    </Typography>
                </div>
                <div className={classes.actions}>
                    {
                        connectAccountDetails
                        ?(
                            <Button
                            color="primary"
                            component="a"
                            href={ connectAccountDetails.url }
                            variant="contained"
                            >
                                Go
                            </Button>
                        )
                        :<></>
                    }
                </div>

                <BottomNavigation value={"aboutStripe"} className={classes.root}>
                    <BottomNavigationAction component="a" target="_blank" href="https://stripe.com/about" label="About stripe" value="aboutStripe" />
                </BottomNavigation>
            </div>
        )
    }

}

const mapStateToProps = state => {
    return {
      loggedInUser: state.auth.loggedInUser,
      loggedInUserPractice: state.auth.loggedInUserPractice
    }
};

const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        getConnectAccount: (id) => {
            return dispatch(getConnectAccountRequest(id)).then((res) => {
            return Promise.resolve(res);
            }, (error) => {
            return Promise.reject(error);
            });
        },
    }
};

StripeDashboard = connect(
  mapStateToProps,
  mapDispatchToProps
)(StripeDashboard);

export default withStyles(styles)(StripeDashboard);