import HttpRequest from '../../utils/HttpRequest';
import PracticeHttpRequest from '../../utils/PracticeHttpRequest';
import Auth from '../../utils/Auth';

import {SNACKBAR_VARIAINT, SYSTEM_ROLES} from '../../utils/Constants';
import {hideLoader, showLoader, showSnackbar} from '../../utils/components/AppLoader/AppLoaderActions'

import {
  createUser,
  getAllUsers,
  getAllDoctorUsers,
  updateCurrentUser
} from "./UserActions";
import {setPracticeForLoggedInUser} from "../Authentication/AuthenticationActions";
import ConstantMessages from '../../utils/Strings';

/**
 * Server API call to get all users
 * - Dispatch getAllUsers action after successful response from server
 * @returns {function(*)}
 */
export function getAllUsersRequest(data) {
  let offset = data.page * data.rowsPerPage;
  let limit = offset + data.rowsPerPage;
  let practiceId = data.PracticeId;
  
  return (dispatch) => {
    dispatch(showLoader());
    const url = `/practice/users?offset=${offset}&limit=${limit}&practiceId=${practiceId}`;

    return new PracticeHttpRequest(dispatch, url, 'get')
      .send()
      .then(res => {
        dispatch(getAllUsers(res.data.data));
        dispatch(hideLoader());
        return Promise.resolve(res.data.data);
      }, error => {
        dispatch(hideLoader());
        return Promise.reject(error);
      });
  };
}

