/**
 * Server API call to get single user
 * @returns {function(*)}
 */
export function getUserRequest(id) {
  return (dispatch) => {
    dispatch(showLoader());
    return new HttpRequest(dispatch, `/users/${id}`, 'get', {})
      .send()
      .then(res => {
        dispatch(hideLoader());
        return Promise.resolve(res.data.data);
      }, error => {
        dispatch(hideLoader());
        return Promise.reject(error);
      });
  };
}

/**
 * Server API call to get user practice
 * @returns {function(*)}
 */
export function getUserPracticeRequest(id) {
  return (dispatch) => {
    dispatch(showLoader());
    return new PracticeHttpRequest(dispatch, `/practice/user/${id}`, 'get', {})
      .send()
      .then(res => {
        Auth.practice(res.data.data);
        dispatch(setPracticeForLoggedInUser(res.data.data));
        dispatch(hideLoader());
        return Promise.resolve(res.data.data);
      }, error => {
        dispatch(hideLoader());
        return Promise.reject(error);
      });
  };
}

/**
 * Server API call to add a new user
 * - Dispatch addUser action after successful response from server
 * @returns {function(*)}
 */
export function addUserRequest(data) {
  return (dispatch) => {
    dispatch(showLoader());
    return new PracticeHttpRequest(dispatch, '/users', 'post', data)
      .send()
      .then(res => {
        let user = res.data.data;
        dispatch(showSnackbar(ConstantMessages.userInviteSuccess, SNACKBAR_VARIAINT.SUCCESS));
        dispatch(createUser(user));
        dispatch(hideLoader());
        return Promise.resolve(res.data.data);
      }, error => {
        dispatch(hideLoader());
        return Promise.reject(error);
      });
  };
}

