/**
 * Server API call to update an user
 * - Dispatch updateUser action after successful response from server
 * @returns {function(*)}
 */
export function updateUserProfileRequest(userId, data) {
  return (dispatch) => {
    dispatch(showLoader());
    return new PracticeHttpRequest(dispatch, `/users/${userId}`, 'patch', data)
      .send()
      .then(res => {
        dispatch(showSnackbar(ConstantMessages.userUpdateSuccessMessage, SNACKBAR_VARIAINT.SUCCESS));
        dispatch(updateCurrentUser(data));
        dispatch(hideLoader());
        return Promise.resolve(data);
      }).catch((error) => {
        dispatch(hideLoader());
        return Promise.reject(error);
      });
  };
}

