import React from 'react';
import {connect} from 'react-redux';
import clsx from 'clsx';
import {withStyles} from '@material-ui/core/styles';
import { Grid, Card, CardContent, Typography, Avatar } from '@material-ui/core';
import PeopleIcon from '@material-ui/icons/PeopleOutlined';
import AttachMoneyIcon from '@material-ui/icons/AccountBalanceWalletRounded';

import HistoricalConsultIcon from '@material-ui/icons/AssignmentTurnedInRounded';
import RequestQueyeIcon from '@material-ui/icons/AssignmentIndRounded';
import UpcomingConsultIcon from '@material-ui/icons/AssignmentReturnedRounded';

import {getPracticeSummary, getDoctorSummary} from "../../../DashboardApiActions";

const styles = theme => ({
    root: {
      height: '100%'
    },
    rootRevenue: {
        height: '100%',
        backgroundColor: theme.palette.primary.main,
        color: theme.palette.primary.contrastText
    },
    content: {
      alignItems: 'center',
      display: 'flex'
    },
    title: {
      fontWeight: 700
    },
    avatar: {
      backgroundColor: '#2196F3',
      height: 56,
      width: 56
    },
    avatarUsers: {
      backgroundColor: theme.palette.success.main,
      height: 56,
      width: 56
    },
    avatarUpcoming: {
      backgroundColor: theme.palette.primary.main,
      color: theme.palette.primary.contrastText,
      height: 56,
      width: 56
    },
    avatarRevenue: {
      backgroundColor: theme.palette.white,
      color: theme.palette.primary.main,
      height: 56,
      width: 56
    },
    icon: {
      height: 32,
      width: 32
    },
    difference: {
      marginTop: theme.spacing(2),
      display: 'flex',
      alignItems: 'center'
    },
    differenceIcon: {
      color: theme.palette.error.dark
    },
    differenceValue: {
      color: theme.palette.error.dark,
      marginRight: theme.spacing(1)
    }
});

class ConsultCard extends React.Component{

    constructor(props) {
        super(props);
  
        this.state = {
          pendingConsult: 0,
          practiceUser: 0,
          revenue: 0,
          upcomingConsult: 0,
          historyConsult: 0,
          isCoordinatorOnly: false,
          isPracticeAdminOnly: false
        };
    }
    
    /**
     * Get dashboard summary for practice/doctor method call when component is render
     * AttachComponent method call when component is render
     */
    componentDidMount = () => {
        if(this.props.loggedInUser && this.props.loggedInUserPractice){
            if(this.props.currentRole.isPracticeAdmin || this.props.currentRole.isCoordinator)
            {
                this.getPracticeDashboardSummary(this.props.loggedInUserPractice.PracticeId);
            }else if(this.props.currentRole.isDoctor){
                this.getDoctorDashboardSummary(this.props.loggedInUserPractice.userId);
            }
        } 

        if(this.props.currentRole.isCoordinator && !this.props.currentRole.isPracticeAdmin && !this.props.currentRole.isDoctor){
            this.setState({
                isCoordinatorOnly: true
            })
        }

        if(this.props.currentRole.isPracticeAdmin && !this.props.currentRole.isCoordinator && !this.props.currentRole.isDoctor){
            this.setState({
                isPracticeAdminOnly: true
            })
        }
    };

    /**
     * Get dashboard summary for practice/doctor method call when component recive new props
     * AttachComponent method call when component recive new props
     */
    componentWillReceiveProps = (nextProps) =>{
        if(nextProps.loggedInUser && nextProps.loggedInUserPractice !== this.props.loggedInUserPractice){
            if(nextProps.currentRole.isPracticeAdmin || nextProps.currentRole.isCoordinator)
            {
                this.getPracticeDashboardSummary(nextProps.loggedInUserPractice.PracticeId);
            }else if(nextProps.currentRole.isDoctor){

                this.getDoctorDashboardSummary(nextProps.loggedInUserPractice.userId);
            }
        }

        if(nextProps.isRefreshCount){
            if(nextProps.currentRole.isPracticeAdmin || nextProps.currentRole.isCoordinator)
            {
                this.getPracticeDashboardSummary(nextProps.loggedInUserPractice.PracticeId);
            }else if(nextProps.currentRole.isDoctor){

                this.getDoctorDashboardSummary(nextProps.loggedInUserPractice.userId);
            }
            
            nextProps.callBackCounts();
        }
    }

    /**
     * Method is used for get dashboard summary/stats for practice admin/co-ordinator
     */
    getPracticeDashboardSummary = (practiceId) => {
        this.props.getPracticeDashboardSummary(practiceId).then((res) => {
            if(res){

                this.setState({
                    pendingConsult: res.pendingConsult?res.pendingConsult:0,
                    practiceUser: res.practiceUser?res.practiceUser:0,
                    revenue: res.revenue?res.revenue:0,
                    historyConsult: res.historyConsult?res.historyConsult:0,
                    upcomingConsult: res.upcomingConsult?res.upcomingConsult:0
                })
            }
        })
    }

    /**
     * Method is used for get dashboard summary/stats for doctor
     */
    getDoctorDashboardSummary = (practiceId) => {
        this.props.getDoctorDashboardSummary(practiceId).then((res) => {
            if(res){
                this.setState({
                    pendingConsult: res.pendingConsult?res.pendingConsult:0,
                    practiceUser: res.practiceUser?res.practiceUser:0,
                    historyConsult: res.historyConsult?res.historyConsult:0,
                    revenue: res.revenue?res.revenue:0,
                    upcomingConsult: res.upcomingConsult?res.upcomingConsult:0
                })
            }
        })
    }
    
    /**
     * Method is used for render the dashboard cards based on authorization
     */
    consultsCardContent = () =>{
        const { className, classes, currentRole, loggedInUserPractice } = this.props;
        const { isCoordinatorOnly } = this.state;
        return (
            <>
                {
                    !currentRole.isPracticeAdmin
                    ?(
                        <Grid item lg={(!loggedInUserPractice.Practice.allowPayment || isCoordinatorOnly)?4:3} sm={6} xl={(!loggedInUserPractice.Practice.allowPayment || isCoordinatorOnly)?4:3} xs={12}>
                            <Card className={clsx(classes.root, className)}>
                                <CardContent>
                                    <Grid container justify="space-between">
                                        <Grid item>
                                            <Typography className={classes.title} 
                                                color="textSecondary" gutterBottom variant="body2">
                                                Pending Consults
                                            </Typography>
                                            <Typography variant="h3">{this.state.pendingConsult || 0}</Typography>
                                        </Grid>
                                        <Grid item>
                                            <Avatar className={classes.avatar}>
                                                <RequestQueyeIcon className={classes.icon} />
                                            </Avatar>
                                        </Grid>
                                    </Grid>
                                </CardContent>
                            </Card>
                        </Grid>
                    )
                    :<></>
                }

                <Grid item lg={(!loggedInUserPractice.Practice.allowPayment || isCoordinatorOnly)?4:3} sm={6} xl={(!loggedInUserPractice.Practice.allowPayment || isCoordinatorOnly)?4:3} xs={12}>
                    <Card className={clsx(classes.root, className)}>
                        <CardContent>
                            <Grid container justify="space-between">
                                <Grid item>
                                    <Typography className={classes.title} 
                                        color="textSecondary" gutterBottom variant="body2">
                                        Upcoming Consults
                                    </Typography>
                                    <Typography variant="h3">{ this.state.upcomingConsult || 0}</Typography>
                                </Grid>
                                <Grid item>
                                    <Avatar className={classes.avatarUpcoming}>
                                        <UpcomingConsultIcon className={classes.icon} />
                                    </Avatar>
                                </Grid>
                            </Grid>
                        </CardContent>
                    </Card>
                </Grid>
                <Grid item lg={(!loggedInUserPractice.Practice.allowPayment || isCoordinatorOnly)?4:3} sm={6} xl={(!loggedInUserPractice.Practice.allowPayment || isCoordinatorOnly)?4:3} xs={12}>
                    <Card className={clsx(classes.root, className)}>
                        <CardContent>
                            <Grid container justify="space-between">
                                <Grid item>
                                    <Typography className={classes.title} 
                                        color="textSecondary" gutterBottom variant="body2">
                                        Historical Consults
                                    </Typography>
                                    <Typography variant="h3">{this.state.historyConsult}</Typography>
                                </Grid>
                                <Grid item>
                                    <Avatar className={classes.avatarUsers}>
                                        <HistoricalConsultIcon className={classes.icon} />
                                    </Avatar>
                                </Grid>
                            </Grid>
                        </CardContent>
                    </Card>
                </Grid>
                {
                    currentRole.isPracticeAdmin?(
                        <Grid item lg={!loggedInUserPractice.Practice.allowPayment?4:3} sm={6} xl={!loggedInUserPractice.Practice.allowPayment?4:3} xs={12}>
                            <Card className={clsx(classes.root, className)}>
                                <CardContent>
                                    <Grid container justify="space-between">
                                        <Grid item>
                                            <Typography className={classes.title} 
                                                color="textSecondary" gutterBottom variant="body2">
                                                Practice Users
                                            </Typography>
                                            <Typography variant="h3">{this.state.practiceUser}</Typography>
                                        </Grid>
                                        <Grid item>
                                            <Avatar className={classes.avatar}>
                                                <PeopleIcon className={classes.icon} />
                                            </Avatar>
                                        </Grid>
                                    </Grid>
                                </CardContent>
                            </Card>
                        </Grid>
                    )
                    :<></>
                }
                
                {
                    !this.state.isCoordinatorOnly && loggedInUserPractice.Practice.allowPayment
                    ?(
                        <>
                            <Grid item lg={!loggedInUserPractice.Practice.allowPayment?4:3} sm={6} xl={!loggedInUserPractice.Practice.allowPayment?4:3} xs={12}>
                                <Card className={clsx(classes.rootRevenue, className)}>
                                    <CardContent>
                                        <Grid container justify="space-between">
                                            <Grid item>
                                                <Typography className={classes.title} color="inherit" 
                                                    gutterBottom variant="body2">
                                                    Revenue
                                                </Typography>
                                                <Typography color="inherit" variant="h3">{ this.props.loggedInUserPractice.Practice.currency } {this.state.revenue}</Typography>
                                            </Grid>
                                            <Grid item>
                                                <Avatar className={classes.avatarRevenue}>
                                                    <AttachMoneyIcon className={classes.icon} />
                                                </Avatar>
                                            </Grid>
                                        </Grid>
                                    </CardContent>
                                </Card>
                            </Grid>
                        </>
                    )
                    :<></>
                }
            </>
        )
    }

    render(){
        return(
            <Grid container spacing={4}>
                { this.consultsCardContent() }
            </Grid>
        )
    }

}

const mapStateToProps = state => {
    return {
      loggedInUser: state.auth.loggedInUser,
      currentRole: state.auth.currentRole,
      loggedInUserPractice: state.auth.loggedInUserPractice
    //   stats: state.stats.stats
    }
};

const mapDispatchToProps = (dispatch) => {
  return {
    getPracticeDashboardSummary: (practiceId) => {
      return dispatch(getPracticeSummary(practiceId)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });

    },
    getDoctorDashboardSummary: (practiceId) => {
        return dispatch(getDoctorSummary(practiceId)).then((res) => {
          return Promise.resolve(res);
        }, (error) => {
          return Promise.reject(error);
        });
  
    }
  }
};

ConsultCard = connect(
  mapStateToProps,
  mapDispatchToProps
)(ConsultCard);

export default withStyles(styles)(ConsultCard);