import React from 'react';
import './PracticeProfile.css';
import {connect} from 'react-redux';

import SetUpPracticeOne from '../SetupPractice/SetupPracticeStepOne';
import {setPageTitle} from "../../../Dashboard/DashboardActions";

class PracticeProfile extends React.Component{

    /**
     * Change page title when component is render
     * AttachComponent method call when component is render
     */
    componentDidMount = () => {
        this.props.updatePageTitle("Practice Profile", false);
    }

    /**
     * Change page title when component is update
     * AttachComponent method call when component is update
     */
    componentDidUpdate = () => {
      this.props.updatePageTitle("Practice Profile", false);
    }

    render(){
        return(
            <div>
                <SetUpPracticeOne />
            </div>
            
        )
    }

}

const mapStateToProps = state => {
    return {
      loggedInUser: state.auth.loggedInUser,
    }
  };
  
  const mapDispatchToProps = (dispatch) => {
    return {
        updatePageTitle: (pageTitle, enablePageTitle) => {
            dispatch(setPageTitle(pageTitle, enablePageTitle));
        }
    }
  };
  
PracticeProfile = connect(
    mapStateToProps,
    mapDispatchToProps
)(PracticeProfile);

export default PracticeProfile;