import React from 'react';
import {connect} from 'react-redux';
import {Redirect} from "react-router-dom";

import {withStyles} from '@material-ui/core/styles';
import {
  Grid,
  Typography
} from '@material-ui/core';

import { signInGoogleRequest, signInFacebookRequest } from '../../AuthenticationApiActions';
import './SignIn.css';
import logo from '../../../../assets/logo.png';

import SocialSignUp from '../SocialSignUp/SocialSignUp';
import AppLoader from "../../../../utils/components/AppLoader/AppLoader";

const styles = theme => ({
  root: {
    backgroundColor: "#fff",
    height: '100%',
  },
  grid: {
    height: '100%'
  },
  quoteContainer: {
    [theme.breakpoints.down('md')]: {
      display: 'none'
    }
  },
  quote: {
    // backgroundColor: theme.palette.neutral,
    height: '100%',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center'
  },
  quoteInner: {
    textAlign: 'center',
    flexBasis: '600px',
    borderRight: '1px rgba(0,0,0,0.12) solid',
    padding: "20% 0%"
  },
  quoteText: {
    color: theme.palette.white,
    fontWeight: 300
  },
  name: {
    marginTop: theme.spacing(3),
    color: theme.palette.white
  },
  bio: {
    color: theme.palette.white
  },
  content: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
    backgroundColor: "#fff"
    
  },
  contentBody: {
    flexGrow: 1,
    display: 'flex',
    alignItems: 'center',
    [theme.breakpoints.down('md')]: {
      justifyContent: 'center'
    },
  },
  form: {
    flexBasis: 700,
    [theme.breakpoints.down('sm')]: {
      paddingLeft: theme.spacing(2),
      paddingRight: theme.spacing(2)
    }
  },
  title: {
    marginTop: theme.spacing(3)
  },
  socialButtons: {
    marginTop: theme.spacing(3)
  }
});

/**
 * Login page shown to the user when session is not present
 */
class SignIn extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      showLoader: false
    }
  }

  /**
   * Method executes when user sign in with google
   */
  signInWithGoogle = (data) => {
    this.setState({showLoader: true}, () => {
      this.props.signInWithGoogle(data).then(() => {
      }, (errer) => {
        this.setState({showLoader: false});
      });
    });
  }

  /**
   * Method executes when user sign in with facebook
   */
  signInWithFacebook = (data) => {
    this.setState({showLoader: true}, () => {
      this.props.signInWithFacebook(data).then(() => {
      }, (errer) => {
        this.setState({showLoader: false});
      });
    });
  }

  /**
   * Render the UI of the login form
   * @returns {XML}
   */
  render() {
    document.title = 'CARE';
    const { classes } = this.props;
    
    if (this.props.loggedInUser) {
      return (<Redirect to="/"/>);
    } else {
      return (
        <>
          <div className={classes.root}>
            <Grid
              className={classes.grid}
              container
            >
              <Grid
                className={classes.quoteContainer}
                item
                lg={6}
              >
                <div className={classes.quote}>
                  <div className={classes.quoteInner}>
                    <div className='application-logo-img-div'>
                      <img className='application-logo-img' src={logo} alt="Loading..."/>
                    </div>
                  </div>
                </div>
              </Grid>
              <Grid
                className={classes.content}
                item
                lg={6}
                xs={12}
              >
                <div className={classes.content}>
                  <div className={classes.contentBody}>
                    <form
                      className={classes.form}
                      onSubmit={this.handleSignIn}
                    >
                      
                      <Typography
                        align="center"
                        className={classes.title}
                        variant="h2"
                      >
                        Login/Signup
                      </Typography>
                      <Typography
                        align="center"
                        color="textSecondary"
                        className="secondaryText"
                        gutterBottom
                      >
                        with your social account
                      </Typography>
                      <Grid
                        className={classes.socialButtons}
                        container
                        spacing={2}
                      >
                        <SocialSignUp
                          signInWithGoogle={(data) => this.signInWithGoogle(data)}
                          signInWithFacebook={(data) => this.signInWithFacebook(data)}
                        />
                      </Grid>
                    </form>
                  </div>
                </div>
              </Grid>
            </Grid>
            <AppLoader showLoader={this.state.showLoader}/>
          </div>
        </>
      );
    }
  }

}

const mapStateToProps = state => {
  return {
    loggedInUser: state.auth.loggedInUser,
    currentRole: state.auth.currentRole,
    signUp: state.auth.signUp,
    loggedInUserPractice: state.auth.loggedInUserPractice
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    signInWithGoogle: (data) => {
      return dispatch(signInGoogleRequest(data)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });

    },
    signInWithFacebook: (data) => {
      return dispatch(signInFacebookRequest(data)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });

    }
  }
};

SignIn = connect(
  mapStateToProps,
  mapDispatchToProps
)(SignIn);

export default withStyles(styles)(SignIn);
