import React from 'react';
import {connect} from 'react-redux';
import {Redirect} from "react-router-dom";

import {getUserPracticeRequest} from '../../../User/UserApiActions';

/**
 * Login page shown to the user when session is not present
 */
class Home extends React.Component {

  /**
   * Get logged in user practice method call when component is render
   * AttachComponent method call when component is render
   */
  componentDidMount = () => {
    if(this.props.loggedInUser && !this.props.loggedInUserPractice){
      this.props.getUserPractice(this.props.loggedInUser.id);
    }
  }

  /**
   * Method is used for redirect the route based on loggedin user practice details
   */
  getRedirectURL = () => {

    const { currentRole, loggedInUserPractice } = Object.assign({}, this.props);

    if(currentRole.isPracticeAdmin){
      if(loggedInUserPractice && loggedInUserPractice.PracticeId){
        if(this.props.loggedInUser.isSubscription){
          if(loggedInUserPractice.Practice.stripeSubscriptionId){
            return (<Redirect to="/dashboard"/>);
          }else{
            return (<Redirect to="/setupPractice"/>);
          }
        }else{
          return (<Redirect to="/dashboard"/>);
        }
        
      }else{
        return (<Redirect to="/setupPractice"/>);
      }
    }else{
      if(loggedInUserPractice.PracticeId){
        return (<Redirect to="/dashboard"/>);
      }else{
        return (<></>);
      }
    }
  }

  render() {
    if (this.props.loggedInUser){
      return this.getRedirectURL();
    }
    else
      return (<Redirect to="/signin"/>);
  }
}

const mapStateToProps = state => {
  return {
    loggedInUser: state.auth.loggedInUser,
    currentRole: state.auth.currentRole,
    loggedInUserPractice: state.auth.loggedInUserPractice
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    getUserPractice: (userId) => {
      dispatch(getUserPracticeRequest(userId));
    }
  }
};

Home = connect(
  mapStateToProps,
  mapDispatchToProps
)(Home);

export default Home;
