import {CREATE_USER, GET_ALL_USERS, GET_ALL_DOCTOR_USERS, UPDATE_USER} from './UserActions';
import {SIGN_OUT} from "../Authentication/AuthenticationActions";

const initialState = {
  users: [],
  doctorUsers: []
};

/**
 * User reducer to update the part of state of the application
 * @param state: Current state value
 * @param action: action received
 * @returns {*} current updated state value
 * @constructor
 */
const UserReducer = (state = initialState, action) => {

  switch (action.type) {

    //Get list of users to be shown
    case GET_ALL_USERS:
      return Object.assign({}, state, {
        users: action.users
      });

    //Get list of users to be shown
    case GET_ALL_DOCTOR_USERS:
      return Object.assign({}, state, {
        doctorUsers: action.users
      });

    //Append newly created user
    case CREATE_USER:
      return Object.assign({}, state, {
        users: [...state.users.rows, action.user]
      });

    //Update user details
    case UPDATE_USER:
      let {users} = state;

      if (users.length === 0) {
        users.push(action.user);
      } else {
        users = users.map((user) => {
          if (user.id === action.user.id) {
            return action.user;
          }
          return user;
        });
      }

      return Object.assign({}, state, {
        users: users
      });

    // Sign out current logged in user
    case SIGN_OUT:
      return Object.assign({}, initialState);

    default:
      return state;
  }
};

export default UserReducer;