import {Button, Grid, Typography, IconButton, 
          Dialog, DialogContent, DialogTitle} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';

import { ValidatorForm, TextValidator} from 'react-material-ui-form-validator';
import Autocomplete from '@material-ui/lab/Autocomplete';

import './AddUser.css';
import AppLoader from "../../../../utils/components/AppLoader/AppLoader";
import MobileNumber from "../../../../utils/components/MobileNumber/MobileNumber";
import Constants from '../../../../utils/Constants';

import { getAllSpecalitiesRequest } from '../../../Authentication/AuthenticationApiActions';

import {ONLY_BLANK_SPACES} from '../../../../utils/Constants';
import UtilHelper from '../../../../utils/UtilHelper';
import ConstantMessages from '../../../../utils/Strings';
import {showSnackbar} from "../../../../utils/components/AppLoader/AppLoaderActions";

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

const styles = theme => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    color: theme.palette.text.secondary,
  },
  chips: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  chip: {
    margin: 2,
  }
});

const dialogTitleStyles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});
const CustomeDialogTitle = withStyles(dialogTitleStyles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <DialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </DialogTitle>
  );
});

class AddUserForm extends React.Component {

    constructor(props) {
      super(props);
      this.state = {
        visible: false,
        selectedRole: undefined,
        isUpdate: false,
        formSubmit: false,
        isPracticeAdmin: false,
        userDetails: {
          displayName: '',
          email: '',
          phone: '',
          roles: '',
          specialization: [],
          consultationFee: ''
        },
        validPhoneNumber: false,
        validRole: false,
        validSpecialization: false
      }
    }

    /**
     * Add custom validation rule in ValidatorForm when component is render
     * AttachComponent method call when component is render
     */
    componentDidMount = () => {
      ValidatorForm.addValidationRule(ONLY_BLANK_SPACES, UtilHelper.validateBlankSpaces);
    };
  
    /**
     * Remove custom validation rule in ValidatorForm when component is unmount
     * AttachComponent method call when component is unmount
     */
    componentWillUnmount() {
      ValidatorForm.removeValidationRule(ONLY_BLANK_SPACES);
    }

    /**
     * Method executes on onChange action of form element
     */
    handleChange = (event, value) => {
      const userDetails = Object.assign({}, this.state.userDetails);
      if(event === 'roles'){
        let isFind = value === Constants.SYSTEM_ROLES.ROLE_DOCTOR;
        let selectedRole = isFind?Constants.SYSTEM_ROLES.ROLE_DOCTOR:undefined;
        userDetails[event] = value;
        userDetails['specialization'] = [];
        this.setState({ userDetails, selectedRole, validRole: value?true:false, validSpecialization: value?selectedRole?false:true:false },()=>{
          
        });
      }else if(event === 'specialization'){
        userDetails[event] = value;
        this.setState({ userDetails, validSpecialization: true });
      }else{
        userDetails[event.target.name] = event.target.value;
        this.setState({ userDetails });
      }
    }

    /**
     * Method executes on submit form action
     */
    handleSubmit = () => {
      const { validPhoneNumber, validRole, validSpecialization } = this.state;
      this.setState({ formSubmit: true }, () => {
        if(this.state.selectedRole === Constants.SYSTEM_ROLES.ROLE_DOCTOR){
          if(validPhoneNumber && validRole && validSpecialization){
            let roleArray = [];
            if(this.state.isPracticeAdmin){
              roleArray.push(Constants.SYSTEM_ROLES.ROLE_PRACTICE_ADMIN);
            }else{}
            const { userDetails } = this.state;
            roleArray.push(userDetails.roles);
            userDetails.roles=roleArray;
            let formData = UtilHelper.trimObject(userDetails);
            this.props.onCreate(formData);
          }
        }else{
          if(validPhoneNumber && validRole){
            let roleArray = [];
            if(this.state.isPracticeAdmin){
              roleArray.push(Constants.SYSTEM_ROLES.ROLE_PRACTICE_ADMIN);
            }else{}
            const { userDetails } = this.state;
            roleArray.push(userDetails.roles);
            userDetails.roles=roleArray;
            let formData = UtilHelper.trimObject(userDetails);
            this.props.onCreate(formData);
          }
        }
      })
    };

    /**
     * Method executes when component receive new props
     */
    componentWillReceiveProps = (nextProps) => {

      if (this.props.visible !== nextProps.visible) {
        if(!nextProps.visible){
          this.onCancel();
        }else{
          this.setState({
            visible: nextProps.visible
          })
        }
      }

      if(this.props.isUpdate !== nextProps.isUpdate){
        if(nextProps.isUpdate){
          let selectedRole = "";

          nextProps.userDetails.specialization = [];
          if(nextProps.userDetails.DoctorSpecialities){
            nextProps.userDetails.DoctorSpecialities.length > 0 && nextProps.userDetails.DoctorSpecialities.map((specialization)=>{
              return nextProps.userDetails.specialization.push(specialization.speciality)
            });
          }

          const isDoctor = nextProps.userDetails.Roles.find((role) => role.name === Constants.SYSTEM_ROLES.ROLE_DOCTOR);
          const isCoordinator = nextProps.userDetails.Roles.find((role) => role.name === Constants.SYSTEM_ROLES.ROLE_COORDINATOR);
          const isPracticeAdmin = nextProps.userDetails.Roles.find((role) => role.name === Constants.SYSTEM_ROLES.ROLE_PRACTICE_ADMIN);
          if(isDoctor){
            nextProps.userDetails.roles = Constants.SYSTEM_ROLES.ROLE_DOCTOR;
            selectedRole = Constants.SYSTEM_ROLES.ROLE_DOCTOR;
          }else if(isCoordinator){
            nextProps.userDetails.roles = Constants.SYSTEM_ROLES.ROLE_COORDINATOR;
          }else{
            nextProps.userDetails.roles = "";
          }
          
          this.setState({
            isUpdate: true,
            formSubmit: false,
            userDetails: {
              displayName: nextProps.userDetails.displayName,
              email: nextProps.userDetails.email,
              phone: nextProps.userDetails.phone,
              roles: nextProps.userDetails.roles,
              specialization: nextProps.userDetails.specialization,
              consultationFee: nextProps.userDetails.consultationFee
            },
            selectedRole: selectedRole,
            validPhoneNumber: nextProps.userDetails.phone?true: false,
            validRole: nextProps.userDetails.roles.length > 0?true:false,
            isPracticeAdmin: isPracticeAdmin?true:false,
            validSpecialization: nextProps.userDetails.specialization.length > 0?true: false
          })
        }else{
          this.setState({
            isUpdate: false,
            formSubmit: false,
            userDetails: {
              displayName: '',
              email: '',
              phone: '',
              roles: '',
              specialization: [],
              consultationFee: ''
            },
            selectedRole: undefined,
            isPracticeAdmin: false,
            visible: false,
            validPhoneNumber: false,
            validRole: false,
            validSpecialization: false
          })
        }
      }
    }

    /**
     * Method executes on cancle to add new user
     */
    onCancel = () => {
      this.setState({
        isUpdate: false,
        formSubmit: false,
        userDetails: {
          displayName: '',
          email: '',
          phone: '',
          roles: '',
          specialization: [],
          consultationFee: ''
        },
        selectedRole: undefined,
        isPracticeAdmin: false,
        visible: false,
        validPhoneNumber: false,
        validRole: false,
        validSpecialization: false
      },()=>{
        this.props.onCancel();
      })

    }

    /**
     * Method executes on update practice mobile number
     */
    updateMobileNumber = (phone, validPhoneNumber) => {
      const userDetails = Object.assign({}, this.state.userDetails);
      userDetails['phone'] = phone;
      this.setState({userDetails, validPhoneNumber});
    };

    render() {
      let {specialities, classes, loggedInUserPractice} = this.props;
      const { visible, isUpdate, userDetails } = this.state;
      
      return (
        <Dialog
          // fullScreen={fullScreen}
          open={visible}
          className="addUserDialog"
          // onClose={this.onCancel}
          aria-labelledby="responsive-dialog-title"
        >
            <CustomeDialogTitle onClose={this.onCancel} id="responsive-dialog-title">{`${isUpdate?"Update User":"Invite User"}`}</CustomeDialogTitle>
            <DialogContent className="addUserDialogContent">
                <ValidatorForm
                  ref="form"
                  onSubmit={() => this.handleSubmit()}
                  noValidate
                  onError={errors => console.log(errors)}
                >
                  <Grid container spacing={0}>
                    <Grid item xs={12}>
                      <TextValidator
                        fullWidth
                        variant="outlined"
                        required
                        label="Name" onChange={this.handleChange}
                        name="displayName" value={userDetails.displayName || ''} 
                        validators={['required', ONLY_BLANK_SPACES, 'maxStringLength:' + Constants.VALIDATIONS.MAXIMUM_CHARACTERS_TEXT_FIELD]}
                        errorMessages={['this field is required', Constants.ERROR_MESSAGES.blank_spaces_not_allowed, ConstantMessages.maximum_allowed_characters_for_text_field]}
                      />
                    </Grid>
                    <Grid item xs={12} className="paddingTopProfileElement">
                      <TextValidator
                        fullWidth
                        required
                        variant="outlined"
                        disabled={isUpdate?true:false}
                        label="Email" onChange={this.handleChange}
                        name="email" value={userDetails.email || ''} validators={['required', 'isEmail']}
                        errorMessages={['this field is required', 'email is not valid']}
                      />
                    </Grid>
                    <Grid item xs={12} className="paddingTopProfileElement">
                      <MobileNumber phone={userDetails.phone || ''}
                                    updateMobileNumber={this.updateMobileNumber}
                                    className="sign-up-input"
                                    required={true}
                      />
                      {
                        this.state.formSubmit && !this.state.validPhoneNumber ?
                          <div className="phone-error">Please enter valid mobile number</div> : ''
                      }
                    </Grid>
                    <Grid item xs={12} className="paddingTopProfileElement">
                      <Autocomplete
                        id="size-small-outlined-multi"
                        size="small"
                        getOptionLabel={(option) => Constants.HUMAN_READABLE_SYSTEM_ROLES[option]}
                        name="roles"
                        options={[Constants.SYSTEM_ROLES.ROLE_DOCTOR, Constants.SYSTEM_ROLES.ROLE_COORDINATOR]}
                        onChange={(e, value)=>this.handleChange("roles", value)}
                        disableClearable
                        value={userDetails.roles || null}
                        renderInput={(params) => {
                          return (
                            <TextValidator name="roles" {...params} fullWidth variant="outlined" 
                              label="Role" placeholder="Role" 
                              validators={['required']}
                              errorMessages={['this field is required']}
                              value={userDetails.roles || null}
                            />
                          )
                        }}
                      />
                    </Grid>
                    {
                      this.state.selectedRole === Constants.SYSTEM_ROLES.ROLE_DOCTOR
                      ?(
                        <>
                          <Grid item xs={12} className="paddingTopProfileElement">
                            <Autocomplete
                              multiple
                              id="size-small-outlined-multi"
                              size="small"
                              name="specialization"
                              options={specialities.map((specialization) => specialization.speciality)}
                              onChange={(e, value)=>this.handleChange("specialization", value)}
                              disableClearable
                              value={userDetails.specialization || null}
                              renderInput={(params) => {
                                return (
                                  <TextValidator name="specialization" {...params} fullWidth variant="outlined" 
                                    label="Specialization" placeholder="Specialization" 
                                    validators={['required']}
                                    errorMessages={['this field is required']}
                                    value={userDetails.specialization || null}
                                  />
                                )
                              }}
                            />
                          </Grid>
                          {
                            loggedInUserPractice.Practice.allowPayment
                            ?(
                              <Grid item xs={12} className="paddingTopProfileElement">
                                <TextValidator
                                  fullWidth
                                  variant="outlined"
                                  required
                                  label={`Consultation Fee (${this.props.loggedInUserPractice.Practice.currency})`} onChange={this.handleChange}
                                  name="consultationFee" value={userDetails.consultationFee || ''} 
                                  validators={['required', ONLY_BLANK_SPACES, 'isFloat', 'maxStringLength:' + Constants.VALIDATIONS.MAXIMUM_CHARACTERS_CONSULT_FEE, 'minNumber:' + Constants.VALIDATIONS.MINIMUM_CONSULT_FEE]}
                                  errorMessages={['this field is required', Constants.ERROR_MESSAGES.blank_spaces_not_allowed, Constants.ERROR_MESSAGES.only_number_accept, ConstantMessages.maximum_allowed_characters_for_consult_fee, ConstantMessages.minimum_amount_greter_than_zero]}
                                />
                              </Grid>
                            )
                            :<></>
                          }
                        </>
                      )
                      :<></>
                    }

                    <Grid container spacing={3} className="paddingTopProfileElement">
                      <Grid item xs={6}>
                        <Button variant="contained" onClick={() => this.onCancel()}
                          className="invite-add-user-btn">Cancel</Button>
                      </Grid>
                      <Grid item xs={6}>
                        <Button type="submit" variant="contained" color="primary"
                          className="invite-add-user-btn">{ isUpdate?"Update":"Invite" }</Button>
                      </Grid>
                    </Grid>
                  </Grid>
                </ValidatorForm>
            </DialogContent>
        </Dialog>
      );
    }
}

class AddUser extends React.Component {

  /**
   * Method executes on update mobile number
   */
  updateMobileNumber = (phone, validPhoneNumber) => {
    this.setState({phone, validPhoneNumber});
  };

  /**
   * Method used for show the add user modal
   */
  showModal = () => {
    this.setState({visible: true, phone: ''});
  };

  /**
   * Method used for hide/cancel to the add user modal
   */
  handleCancel = () => {
    this.setState({
      visible: false,
      isUpdate: false,
      userDetails: {},
    }, () => {
      this.props.cancleEditUser();
    });
  };

  /**
   * Method to add new user
   */
  handleAddUser = (data) => {
    this.setState({formSubmit: true}, () => {
      this.setState({showLoader: true}, () => {
          if(this.state.isUpdate){

            this.props.handleUpdateUser(this.state.userDetails.userId, data).then(response => {
              this.props.showSnackbar(ConstantMessages.userHasBeenUpdated, Constants.SNACKBAR_VARIAINT.INFO);
              this.setState({showLoader: false, visible: false});
              this.handleCancel();
            }, err => {
              this.setState({showLoader: false});
            });
          }else{
            this.props.handleInviteUser(data).then(response => {
              this.props.showSnackbar(ConstantMessages.userHasBeenInvited, Constants.SNACKBAR_VARIAINT.INFO);
              this.setState({showLoader: false, visible: false});
              this.handleCancel();
            }, err => {
              this.setState({showLoader: false});
            });
          }
      });
    });
  };

  /**
   * Method to create form ref
   */
  saveFormRef = (formRef) => {
    this.formRef = formRef;
  };

  constructor(props) {
    super(props);

    this.state = {
      visible: false,
      showLoader: false,
      formSubmit: false,
      userDetails:{},
      isUpdate: false
    }

  };

  /**
   * Get all specialities when component is render
   * AttachComponent method call when component is render
   */
  componentDidMount() {
    this.props.getAllSpecalities();
    if(this.props.isUpdate){
      this.setState({
        isUpdate: true,
        userDetails: this.props.userDetails,
        visible: true
      })
    }
  }

  /**
   * AttachComponent method call when component receive new props
   */
  componentWillReceiveProps = (newProps) => {

    if(newProps.isUpdate !== this.state.isUpdate){
      if(newProps.userDetails.DoctorSpecialities && newProps.userDetails.DoctorSpecialities.length>0){
        let specialization = [];
        newProps.userDetails.DoctorSpecialities.map((DoctorSpecialitie)=>{
          return specialization.push(DoctorSpecialitie.speciality);
        })
        newProps.userDetails.specialization=specialization;
      }

      if(newProps.isUpdate){
        this.setState({
          isUpdate: true,
          userDetails: newProps.userDetails,
          phone: newProps.userDetails.phone,
          validPhoneNumber: true,
          visible: true
        })

        
      }
    }
  }

  render() {
    const {specialities, loggedInUser, classes, loggedInUserPractice} = this.props;

    return (
      <div className="add-user-btn-div">

        <div className={classes.root}>
          <Grid container>
            <Grid item xs={12}>
                {
                  <>
                    <span className="informationHeader">User Management</span>
                    {
                      this.props.onSkipAddUser
                      ?(
                        <Button variant="contained" color="default" className="buttonRight skipButton" 
                          onClick={() => this.props.onSkipAddUser()}>
                          Skip
                        </Button>
                      )
                      :<></>
                    }
                    <Button variant="contained" color="primary" className="buttonRight" icon="plus" onClick={() => this.showModal()}>
                      Invite User
                    </Button>
                    
                  </>
                }

                <AddUserForm
                  visible={this.state.visible}
                  isUpdate={this.state.isUpdate}
                  userDetails= {this.state.userDetails}
                  onCancel={this.handleCancel}
                  onCreate={this.handleAddUser}
                  specialities={specialities}
                  loggedInUser={loggedInUser}
                  loggedInUserPractice={loggedInUserPractice}
                  currentRole={this.props.currentRole}
                  formSubmit={this.state.formSubmit}
                  classes={classes}
                  validPhoneNumber={this.state.validPhoneNumber}
                />

            </Grid>
          </Grid>
        </div>

        <AppLoader showLoader={this.state.showLoader}/>
      </div>
    );
  };
}

const mapStateToProps = (state) => {
  return {
    loggedInUser: state.auth.loggedInUser,
    currentRole: state.auth.currentRole,
    specialities: state.auth.specialities,
    loggedInUserPractice: state.auth.loggedInUserPractice,
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    getAllSpecalities: () => {
      return dispatch(getAllSpecalitiesRequest()).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },
    showSnackbar: (message, variant) => {
      dispatch(showSnackbar(message, variant));
    }
  }
};

AddUser = connect(
  mapStateToProps,
  mapDispatchToProps
)(AddUser);

export default withStyles(styles)(AddUser);
